import { FC } from 'react';
import ErrorScreen from '../components/common/screens/ErrorScreen';
import useRole from '../hooks/useRole';

interface RequirePermissionProps {
  roles: Role[];
}

const RequirePermission: FC<RequirePermissionProps> = ({ roles, children }) => {
  const permitted = useRole(roles);
  return permitted ? <>{children}</> : <ErrorScreen />;
};

export default RequirePermission;
