import { Stripe } from '@stripe/stripe-js';
import { createContext, Dispatch, SetStateAction } from 'react';
import { CampaignsAction } from '../reducer/campaignsReducer';

interface AppContextType {
  campaigns: Campaigns | null;
  dispatchCampaigns: Dispatch<CampaignsAction>;
}

interface DonationContextType {
  stripePromise: Promise<Stripe | null>;
  stripeIntent: any;
  setStripeIntent: Dispatch<SetStateAction<any>>;
}
interface AuthContextType {
  user: User;
  paymentMethod: {
    card: {
      brand: string;
      last4: string;
    };
  } | null;
  signin: (data: SigninRequest, callback: VoidFunction) => void;
  signout: (callback: VoidFunction) => void;
  setUser: Dispatch<SetStateAction<User>>;
}

const AppContext = createContext<AppContextType | null>(null);
export const DonationContext = createContext<DonationContextType | null>(null);
export const AuthContext = createContext<AuthContextType | null>(null);

export default AppContext;
