import { HTMLAttributes, PropsWithChildren, Ref } from 'react';
import clsx from 'clsx';

interface ViewCommonProps extends HTMLAttributes<HTMLDivElement> {
  className?: string;
  container?: boolean;
  row?: boolean;
  scroll?: 'vertical' | 'horizontal' | boolean;
  nodeRef?: Ref<HTMLDivElement>;
}

interface ViewWithScreenProps {
  screen?: boolean;
  content?: never;
}
interface ViewWithContentProps {
  content?: boolean;
  screen?: never;
}

export type ViewProps = ViewCommonProps &
  (ViewWithScreenProps | ViewWithContentProps);

const View = ({
  screen = false,
  content = false,
  container = false,
  scroll = 'vertical',
  row = false,
  className = '',
  nodeRef,
  ...rest
}: PropsWithChildren<ViewProps>) => {
  return (
    <div
      className={clsx('flex', className, {
        'flex-col': !row,
        'h-screen': screen,
        'h-content': content,
        'w-screen': screen && !container,
        container: container,
        'overflow-hidden': !scroll,
        'scrollbar-hide': scroll && (screen || content),
      })}
      ref={nodeRef}
      {...rest}
    />
  );
};

export default View;
