import { FC, useEffect, useState } from 'react';
import { AuthContext } from '.';
import ApiService from '../service';
import { errorHandler } from '../utils';

const AuthProvider: FC = ({ children }) => {
  const [user, setUser] = useState<User>(null!);
  const [paymentMethod, setPaymentMethod] = useState<{
    card: {
      brand: string;
      last4: string;
    };
  } | null>(null);

  const signin = (data: SigninRequest, callback: VoidFunction) => {
    ApiService.signin(data)
      .then(({ data }) => {
        setUser(data.user);
        localStorage.setItem('payload', JSON.stringify(data.payload));
      })
      .catch(errorHandler)
      .finally(callback);
  };

  const signout = (callback: VoidFunction) => {
    ApiService.signout()
      .then(() => {
        setUser(null!);
        localStorage.removeItem('payload');
      })
      .catch(errorHandler)
      .finally(callback);
  };

  useEffect(() => {
    if (user?.role === 'individual') {
      ApiService.getPaymentMethod().then(setPaymentMethod);
    }
  }, [user]);

  const context = { paymentMethod, user, signin, signout, setUser };
  return (
    <AuthContext.Provider value={context}>{children}</AuthContext.Provider>
  );
};

export default AuthProvider;
