import { ChangeEvent, useState } from 'react';

interface TouchInputProps {
  name: string;
  value: string;
  type: 'text' | 'number';
  onChange: (e: ChangeEvent<HTMLInputElement>) => void;
  formatter: (value: string) => string;
  placeholder?: string;
}

const TouchInput = ({
  name,
  type,
  value,
  onChange,
  formatter,
  placeholder = '',
}: TouchInputProps) => {
  const [editing, setEditing] = useState(false);

  return (
    <>
      {editing ? (
        <input
          type={type}
          name={name}
          value={value}
          onChange={onChange}
          onBlur={() => setEditing(false)}
          className='p-0 leading-none border-0 bg-transparent text-current text-center inline-block focus:ring-0 caret-primary-600'
          style={{
            fontSize: 'inherit',
            fontWeight: 'inherit',
            lineHeight: 'inherit',
          }}
          placeholder={editing ? '' : placeholder}
        />
      ) : (
        <span onClick={() => setEditing(true)}>
          {value ? formatter(value) : placeholder}
        </span>
      )}
    </>
  );
};

export default TouchInput;
