import { useState } from 'react';
import { Link } from 'react-router-dom';
import { useQuery } from 'react-query';
import clsx from 'clsx';
import { Button, Container, Text, View } from '../lib';
import useRole from '../hooks/useRole';
import Banner from '../components/common/Banner';
import Dialog from '../components/common/Dialog';
import CampaignCreate from '../components/campaign/CampaignCreate';
import Campaigns from '../components/campaign/Campaigns';
import { getCampaigns, getStripeAccount } from '../service';
import { uniqMerge } from '../utils';

import bannerFeedIndividual from '../assets/img/banner-feed-individual-3x.png';
import bannerFeedIndividualWeb from '../assets/img/banner-feed-individual-web-3x.png';
import bannerFeedOrganization from '../assets/img/banner-feed-organization-3x.png';

const Dashboard = () => {
  const [page, setPage] = useState(1);
  const [campaigns, setCampaigns] = useState<Campaigns>();
  const isIndividual = useRole(['individual']);
  const isOrganization = useRole(['organization']);

  const { isLoading: bankInfFetching, data: bankInfo } = useQuery(
    'bank-info',
    getStripeAccount
  );

  const { isLoading, refetch } = useQuery(
    ['campaigns', page],
    () => getCampaigns({ onlyUnfulfilled: true, page }),
    {
      onSuccess(data) {
        setCampaigns((prevData) => uniqMerge(data, prevData));
      },
    }
  );

  return (
    <View content>
      <Banner sticky={!!isOrganization}>
        <Container className='py-5'>
          {isIndividual && (
            <>
              <img
                src={bannerFeedIndividual}
                className='lg:hidden'
                alt='Banner'
              />
              <img
                src={bannerFeedIndividualWeb}
                alt='Banner'
                className='hidden lg:block'
              />
            </>
          )}
          {isOrganization &&
            !bankInfFetching &&
            (bankInfo?.details_submitted ? (
              <Dialog
                activator={(setOpen) => (
                  <>
                    <img
                      src={bannerFeedOrganization}
                      alt='Banner'
                      onClick={() => setOpen(true)}
                      className='cursor-pointer lg:hidden'
                    />
                    <View
                      row
                      className='items-center justify-between hidden w-full gap-4 lg:flex'
                    >
                      <Text type='h1'>Create new campaign</Text>
                      <Button onClick={() => setOpen(true)}>
                        New campaign
                      </Button>
                    </View>
                  </>
                )}
              >
                {(setOpen) => (
                  <CampaignCreate
                    onClose={() => {
                      refetch();
                      setOpen(false);
                    }}
                  />
                )}
              </Dialog>
            ) : (
              <View className='space-y-4 container-mini'>
                <Text type='h5' className='text-center text-red-500'>
                  To start receiving donations or creating campaigns, please add
                  your banking information.
                </Text>
                <Button tag={Link} to='/settings'>
                  Go to Settings
                </Button>
              </View>
            ))}
        </Container>
      </Banner>
      <View container className='flex-1'>
        {isIndividual && (
          <Text type='h3' className='sticky top-0 z-10 py-5 bg-gray-200'>
            Recent Campaigns
          </Text>
        )}
        <div className={clsx('space-y-5 pb-5', { 'mt-5': isOrganization })}>
          <Campaigns
            loading={isLoading}
            campaigns={campaigns}
            refetch={refetch}
            setPage={setPage}
            action
          />
        </div>
      </View>
    </View>
  );
};

export default Dashboard;
