import { FC, forwardRef } from 'react';
import clsx from 'clsx';
import Loader4FillIcon from 'remixicon-react/Loader4FillIcon';

type ButtonType = 'button' | 'submit' | 'reset';
type AppearanceType = 'solid' | 'outline' | 'minimal';
type IntentType = 'primary' | 'error' | 'secondary';

interface ButtonCommonProps {
  /**
   * Define the variant of component
   */
  appearance?: AppearanceType;
  /**
   * Expand full width
   */
  block?: boolean;
  /**
   * Additional styles to customize button
   */
  className?: string;
  /**
   * Disable state of button
   */
  disabled?: boolean;
  /**
   * If  `true` icon button will be square
   */
  iconOnly?: boolean;
  /**
   *  Define intent of component
   */
  intent?: IntentType;
  /**
   * Button will be rounded or default shape
   */
  rounded?: 'lg' | 'full';
  /**
   * Button will be small or default size
   */
  small?: boolean;
  /**
   * Button will be in loading state
   */
  loading?: boolean;
}

interface ButtonAsButtonProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    ButtonCommonProps {
  /**
   * The element type for button as Other
   */
  tag?: 'button';
  /**
   * Define button type of component
   */
  type?: ButtonType;
}

interface ButtonAsAnchorProps
  extends React.AnchorHTMLAttributes<HTMLAnchorElement>,
    ButtonCommonProps {
  /**
   * The element type for button
   */
  tag: 'a';
  /**
   * Allows to pass url to a element
   */
  href: string;
}
interface ButtonAsLabelProps
  extends React.LabelHTMLAttributes<HTMLLabelElement>,
    ButtonCommonProps {
  /**
   * The element type for button
   */
  tag: 'label';
  /**
   * Allows htmlFor pass for to a element
   */
  htmlFor?: string;
}

interface ButtonAsOtherProps
  extends React.AnchorHTMLAttributes<HTMLAnchorElement>,
    ButtonCommonProps {
  /**
   * The element type for button as Other
   */
  tag: React.ComponentType<any>;
  /**
   * custom component related props
   */
  [key: string]: any;
}

export type ButtonProps =
  | ButtonAsButtonProps
  | ButtonAsAnchorProps
  | ButtonAsLabelProps
  | ButtonAsOtherProps;

const Button: FC<ButtonProps> = forwardRef<HTMLElement, ButtonProps>(
  (
    {
      tag: Tag = 'button',
      block = false,
      disabled = false,
      intent = 'primary',
      iconOnly = false,
      small = false,
      rounded = 'lg',
      loading = false,
      className = '',
      children,
      ...rest
    },
    ref
  ) => (
    <Tag
      ref={ref}
      className={clsx(
        'font-bold border-2 transition text-center focus:outline-none focus:ring-inset focus:ring-primary-600 disabled:cursor-not-allowed disabled:text-gray-400',
        {
          'space-x-2 flex justify-center items-center': loading,
          'rounded-lg': rounded === 'lg',
          'rounded-full': rounded === 'full',
          'flex justify-center items-center': iconOnly,
          'px-8 py-2.5': !iconOnly && !small,
          'px-3 py-1 text-sm': !iconOnly && small,
          'w-11 h-11': iconOnly && !small,
          'w-8 h-8': iconOnly && small,
          'bg-primary text-white border-primary hover:bg-primary-600 hover:border-primary-600 disabled:border-gray-300 disabled:hover:border-gray-300 disabled:bg-gray-300':
            intent === 'primary',
          'bg-error text-white border-error hover:bg-error-600 hover:border-error-600 disabled:border-gray-300 disabled:hover:border-gray-300 disabled:bg-gray-300':
            intent === 'error',
          'bg-white text-primary border-primary  hover:border-primary-600 hover:text-primary-600 disabled:border-gray-400 disabled:hover:border-gray-400':
            intent === 'secondary',
          'block w-full': block,
          'inline-block': !block,
          disabled: disabled,
        },
        className
      )}
      disabled={disabled}
      {...rest}
    >
      {loading && (
        <Loader4FillIcon className='animate-spin' size={small ? 18 : 24} />
      )}
      {loading && iconOnly ? null : loading ? (
        <span>{children}</span>
      ) : (
        children
      )}
    </Tag>
  )
);

export default Button;
