import HttpClient from './HttpClient';

const httpClient = new HttpClient(process.env.REACT_APP_HOST_URL + '/api/v1');
const payload: Payload = JSON.parse(localStorage.getItem('payload') as string);

if (payload) {
  httpClient.addHeader(
    'Authorization',
    `${payload.type} ${payload.accessToken}`
  );
}

