export function checkHealth() {
  return new Promise<Response>((resolve, reject) => {
    httpClient
      .get('/')
      .then(({ data }) => {
        resolve(data.data);
      })
      .catch(reject);
  });
}

/* -------------------------------------------------------------------------- */
/*                               Authentication                               */
/* -------------------------------------------------------------------------- */
export function signup(
  data: SignupIndividualRequest | SignupOrganizationRequest
) {
  return new Promise<SuccessResponse<SigninUser | null>>((resolve, reject) => {
    httpClient
      .post('/register', { data })
      .then(({ data }) => {
        resolve(data);
        httpClient.addHeader(
          'Authorization',
          'Bearer ' + data.data.payload.accessToken
        );
      })
      .catch(({ data }) => reject(data));
  });
}

export function signin(data: SigninRequest) {
  return new Promise<SuccessResponse<SigninUser>>((resolve, reject) => {
    httpClient
      .post('/login', { data })
      .then(({ data }) => {
        resolve(data);
        httpClient.addHeader(
          'Authorization',
          'Bearer ' + data.data.payload.accessToken
        );
      })
      .catch(({ data }) => reject(data));
  });
}

export function signout() {
  return new Promise<void>((resolve, reject) => {
    httpClient
      .post('/logout')
      .then(() => {
        httpClient.removeHeader('Authorization');
        resolve();
      })
      .catch(reject);
  });
}

