import { useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { ResponseLayout } from '../../../layout';
import { Button } from '../../../lib';
import { confirmDonation } from '../../../service';
import { successHandler } from '../../../utils';

import success from '../../../assets/img/illustrations/success.png';

const SuccessScreen = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();

  useEffect(() => {
    const paymentIntent = searchParams.get('payment_intent');
    if (paymentIntent) {
      confirmDonation({ payment_intent: paymentIntent }).then(successHandler);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const deleteSearchParams = (name: string) => {
    const paymentIntent = searchParams.get(name);
    if (paymentIntent) {
      searchParams.delete(name);
    }
  };

  const handleDone = () => {
    deleteSearchParams('redirect_status');
    deleteSearchParams('payment_intent');
    setSearchParams(searchParams);

    navigate('/', { replace: true });
  };

  return (
    <ResponseLayout
      title='Success!'
      subtitle='Thanks for donating!'
      image={success}
    >
      <Button onClick={handleDone}>Done</Button>
    </ResponseLayout>
  );
};

export default SuccessScreen;
