import clsx from 'clsx';
import {
  Dispatch,
  ReactNode,
  SetStateAction,
  useEffect,
  useRef,
  useState,
} from 'react';
import { CSSTransition } from 'react-transition-group';
import { Portal, View } from '../../lib';

interface DialogProps {
  children: (setOpen: Dispatch<SetStateAction<boolean>>) => ReactNode;
  activator?: (setOpen: Dispatch<SetStateAction<boolean>>) => ReactNode;
  transition?: 'slide-up' | 'slide-down' | 'zoom-in' | 'zoom-out';
  persistent?: boolean;
  isOpen?: boolean;
}

const Dialog = ({
  persistent = false,
  transition = window.innerWidth < 1024 ? 'slide-up' : 'zoom-in',
  isOpen = false,
  activator,
  children,
}: DialogProps) => {
  const [open, setOpen] = useState(isOpen);
  const [showOverlay, setShowOverlay] = useState(false);
  const nodeRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    setOpen(isOpen);
  }, [isOpen]);

  return (
    <View>
      {activator && activator(setOpen)}
      {showOverlay && (
        <div
          className='fixed inset-0 z-40 backdrop-blur-sm bg-white/30'
          aria-hidden='true'
          onClick={() => !persistent && setOpen(false)}
        />
      )}
      <Portal>
        <CSSTransition
          in={open}
          timeout={200}
          classNames={transition}
          unmountOnExit
          onEnter={() => setShowOverlay(true)}
          onExited={() => setShowOverlay(false)}
          nodeRef={nodeRef}
        >
          <View
            className={clsx('fixed z-50 bg-white border border-gray-300', {
              'left-0 right-0 bottom-0 shadow-lg-invert rounded-t-3xl':
                transition === 'slide-up',
              'left-0 right-0 top-0 shadow-lg rounded-b-3xl':
                transition === 'slide-down',

              'shadow-lg rounded-3xl':
                transition === 'zoom-in' || transition === 'zoom-out',
            })}
            nodeRef={nodeRef}
          >
            {children(setOpen)}
          </View>
        </CSSTransition>
      </Portal>
    </View>
  );
};

export default Dialog;
