import { toast } from 'react-toastify';
import uniqBy from 'lodash.uniqby';

export const getAssetUrl = (filepath: string): string => {
  if (!filepath) return '';
  return process.env.REACT_APP_ASSET_HOST_URL + '/' + filepath;
};

export const fileToBase64 = (file: File): Promise<string> =>
  new Promise<string>((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      resolve(reader.result as string);
    };
    reader.onerror = (error) => reject(error);
  });

export const currencyFormat = (
  number: number,
  options?: { decimal?: number }
): string =>
  number.toLocaleString('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: options?.decimal ?? 2,
    maximumFractionDigits: options?.decimal ?? 2,
  });

export const errorHandler = (error: ErrorResponse | string) => {
  toast(typeof error === 'string' ? error : error.message, {
    type: 'error',
  });
};

export const successHandler = <T>(response: SuccessResponse<T>) => {
  toast(response.message, {
    type: 'success',
  });
};

/* -------------------------------------------------------------------------- */
/*                              Device Detection                              */
/* -------------------------------------------------------------------------- */
export const iPhoneVersion = () => {
  var iHeight = window.screen.height;
  var iWidth = window.screen.width;

  if (iWidth === 414 && iHeight === 896) {
    return 'iPhone-Xmax-Xr';
  } else if (iWidth === 375 && iHeight === 812) {
    return 'iPhone-X-Xs';
  } else if (iWidth === 320 && iHeight === 480) {
    return 'iPhone-4';
  } else if (iWidth === 375 && iHeight === 667) {
    return 'iPhone-6';
  } else if (iWidth === 414 && iHeight === 736) {
    return 'iPhone-6+';
  } else if (iWidth === 320 && iHeight === 568) {
    return 'iPhone-5';
  } else if (iHeight <= 480) {
    return 'iPhone-2-3';
  }
  return 'iPhone-12';
};

export const isIphone = () => {
  return !!navigator.userAgent.match(/iPhone/i);
};

export const formatDate = (date: Date) =>
  new Date(date).toLocaleDateString('en-EN', {
    weekday: 'long',
    year: 'numeric',
    month: 'long',
    day: 'numeric',
  });

declare let window: any;

const openUrlInAppBrowser: (
  url: string,
  onExit?: VoidFunction,
  onLoadStart?: (e: any) => void
) => any = (url, onExit, onLoadStart) => {
  const inAppBrowserRef = window.cordova.InAppBrowser.open(
    url,
    '_system',
    'location=yes,hidden=yes,clearsessioncache=yes'
  );

  if (onExit) {
    inAppBrowserRef.addEventListener('exit', async () => {
      onExit();
    });
  }

  if (onLoadStart) {
    inAppBrowserRef.addEventListener('loadstart', async (e: any) => {
      onLoadStart(e);
    });
  }

  return inAppBrowserRef;
};

export const openCustomURL: (
  url: string,
  onExit?: VoidFunction,
  onLoadStart?: (e: any) => void
) => any | void = (url, onExit, onLoadStart) => {
  if (typeof window.cordova !== 'undefined') {
    return openUrlInAppBrowser(url, onExit, onLoadStart);
  } else {
    window.open(url);
  }
};

export const getAppURL = (url: string) => {
  const searchParams = new URL(url).searchParams;
  const id = searchParams.get('id');
  const type = searchParams.get('type') as DonationType;

  return '/' + type + 's/' + id;
};

/* -------------------------------------------------------------------------- */
/*                              Push Notification                             */
/* -------------------------------------------------------------------------- */
export const oneSignalInit = async (
  redirect: (url: string) => void,
  callback: () => Promise<void>
) => {
  // Uncomment to set OneSignal device logging to VERBOSE
  // window.plugins.OneSignal.setLogLevel(6, 0);

  // NOTE: Update the setAppId value below with your OneSignal AppId.
  const appId = process.env.REACT_APP_ONESIGNAL_APP_ID;
  window.plugins.OneSignal.setAppId(appId);
  window.plugins.OneSignal.setNotificationOpenedHandler(
    ({
      notification: {
        additionalData: { type, id },
      },
    }: {
      notification: {
        notificationId: string;
        title: string;
        body: string;
        additionalData: { type: FavoriteType; id: number };
      };
    }) => {
      redirect('/' + type + '/' + id);
    }
  );

  // iOS - Prompts the user for notification permissions.
  // * Since this shows a generic native prompt, we recommend instead using an In-App Message to prompt for notification permission (See step 6) to better communicate to your users what notifications they will get.
  window.plugins.OneSignal.promptForPushNotificationsWithUserResponse(
    (accepted: Boolean) => {
      if (accepted && !window.acceptedNotificationPromptShown) {
        window.acceptedNotificationPromptShown = true;
      }
    }
  );

  await callback();
};

export const addDays = (date: Date, days: number): Date => {
  const result = new Date(date);
  result.setDate(result.getDate() + days);
  return result;
};

export const isWeb = typeof window.cordova === 'undefined';

export const uniqMerge = <T extends { id: ID }>(
  newData: { items: T[]; meta: Meta },
  prevData?: { items: T[]; meta: Meta }
) => {
  console.log(newData);

  return prevData
    ? {
        items: uniqBy([...prevData.items, ...newData.items], ({ id }) => id),
        meta: newData.meta,
      }
    : newData;
};
