import { useCallback, useEffect } from 'react';
import { Outlet, useNavigate, useSearchParams } from 'react-router-dom';
import useAuth from '../hooks/useAuth';
import { Toolbar } from '../lib';
import ApiService from '../service';
import { getAppURL, oneSignalInit } from '../utils';

const AppLayout = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const initPushNotificationId = useCallback(async () => {
    (window as any)?.plugins?.OneSignal?.getDeviceState(
      async (stateChanges: { userId: string }) => {
        console.log({ stateChanges });

        if (user) {
          if (typeof stateChanges.userId !== 'undefined') {
            console.log('Update Push Notification ID');

            await ApiService.updatePushNotificationId({
              pushNotificationId: stateChanges.userId,
            });
          }
        }
      }
    );
  }, [user]);

  useEffect(() => {
    const succeeded = searchParams.get('redirect_status') === 'succeeded';
    if (succeeded) {
      navigate('/success', { replace: true });
    }
  }, [navigate, searchParams]);

  useEffect(() => {
    (window as any).handleOpenURL = function (url: string) {
      setTimeout(() => {
        navigate(getAppURL(url));
      }, 1000); //* Taking 1000ms for app ready if it isn't.
    };
  }, [navigate]);

  useEffect(() => {
    document.addEventListener(
      'deviceready',
      () => {
        oneSignalInit((url) => navigate(url), initPushNotificationId);
      },
      false
    );
  }, [initPushNotificationId, navigate]);

  return (
    <main>
      <Toolbar className='bottom-0 lg:bottom-auto lg:top-0 shadow-lg-invert lg:shadow-md' />
      <section className='lg:pt-20'>
        <Outlet />
      </section>
    </main>
  );
};

export default AppLayout;
