import { FC } from 'react';
import CloseFillIcon from 'remixicon-react/CloseFillIcon';
import { Button, View } from '../../lib';
import { isIphone, openCustomURL } from '../../utils';
import Dialog from './Dialog';

interface AnchorLinkProps {
  href: string;
  className?: string;
  tag?: 'span' | 'div';
}

const AnchorLink: FC<AnchorLinkProps> = ({
  tag: Component = 'span',
  href,
  children,
  ...rest
}) => (
  <Dialog
    activator={(setOpen) => (
      <Component
        onClick={() => (isIphone() ? setOpen(true) : openCustomURL(href))}
        {...rest}
      >
        {children}
      </Component>
    )}
  >
    {(setOpen) => (
      <View content>
        <Button
          iconOnly
          rounded='full'
          className='m-5 bg-gray border-gray'
          onClick={() => setOpen(false)}
        >
          <CloseFillIcon size={24} />
        </Button>
        <iframe src={href} title='GiveUmmah' width='100%' height='100%' />
      </View>
    )}
  </Dialog>
);

export default AnchorLink;
