import { FC } from 'react';
import clsx from 'clsx';

type TextType = 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p' | 'small';

interface TextProps {
  type?: TextType;
  className?: string;
}

const getTextClassNames = (type: TextType) => {
  switch (type) {
    case 'h1':
      return 'text-gray-600 font-bold text-[2rem] leading-9';
    case 'h2':
      return 'text-gray-600 font-bold text-xl';
    case 'h3':
      return 'text-gray-600 font-bold';
    case 'h4':
      return 'text-gray-600';
    case 'p':
      return;
    case 'h5':
      return 'text-gray-600 text-sm';
    case 'small':
      return 'text-sm';
    case 'h6':
      return 'text-gray-600 text-xs';
  }
};

const Text: FC<TextProps> = ({ type: Tag = 'p', className, children }) => (
  <Tag className={clsx(getTextClassNames(Tag), className)}>{children}</Tag>
);

export default Text;
