import useAuth from '../hooks/useAuth';
import Individual from '../components/profile/Individual';
import Organization from '../components/profile/Organization';

const Profile = () => {
  const { user } = useAuth();

  switch (user.role) {
    case 'organization':
      return <Organization user={user} />;
    case 'individual':
      return <Individual user={user} />;
    default:
      return null;
  }
};

export default Profile;
