import { useQuery } from 'react-query';
import BackButton from '../components/common/BackButton';
import SavedBankingCard from '../components/common/cards/SavedBankingCard';
import BankInformationDialog from '../components/settings/BankInformationDialog';
import { Text, View } from '../lib';
import { totalRaisedAmount } from '../service';
import { currencyFormat } from '../utils';

const AmountRaised = () => {
  const { data: total } = useQuery('total-raised-amount', totalRaisedAmount);

  return (
    <View screen container className='pt-12'>
      <View container row className='items-center mb-8 space-x-4'>
        <BackButton />
        <Text type='h1'>Amount Raised</Text>
      </View>
      <View container>
        <Text type='h2' className='mb-8'>
          Bank Details
        </Text>
        <BankInformationDialog>
          <SavedBankingCard />
        </BankInformationDialog>
        <Text type='h2' className='mt-12 mb-8'>
          Total amount raised
        </Text>
        <div className='flex items-center justify-center p-5 text-3xl font-bold leading-none text-center text-white border-0 rounded-lg border-primary bg-primary'>
          {currencyFormat(Number(total ? total[0]?.totalRaisedAmount : 0))}
        </div>
      </View>
    </View>
  );
};

export default AmountRaised;
