import { ReactNode } from 'react';
import clsx from 'clsx';
import { Button, View } from '../../lib';
import { ViewProps } from '../../lib/View';

export type TabItem = {
  value: string;
  content: ReactNode;
};

type TabProps = ViewProps & {
  tabs: TabItem[];
  currentTab: TabItem;
  onTabChange: (currentTab: TabItem) => void;
};

const Tab = ({ tabs, currentTab, onTabChange, ...rest }: TabProps) => {
  return (
    <View row {...rest}>
      {tabs.map((tab) => (
        <Button
          className={clsx(
            'px-0 flex-1 border-transparent focus:border-transparent focus:ring-0 hover:border-transparent',
            {
              'bg-primary-100': currentTab.value === tab.value,
              'bg-transparent text-current': currentTab.value !== tab.value,
            }
          )}
          intent='secondary'
          onClick={() => onTabChange(tab)}
          key={tab.value}
        >
          {tab.content}
        </Button>
      ))}
    </View>
  );
};

export default Tab;
