import { useEffect, useRef } from 'react';

function fillInAddress(autocomplete: google.maps.places.Autocomplete) {
  const place = autocomplete.getPlace();
  let address1 = '';
  let postcode = '';

  const address = {} as Address;

  for (const component of place.address_components as google.maps.GeocoderAddressComponent[]) {
    const componentType = component.types[0];

    switch (componentType) {
      case 'street_number': {
        address1 = `${component.long_name} ${address1}`;
        address.street_number = address1;
        break;
      }

      case 'route': {
        address1 += component.short_name;
        address.route = address1;
        break;
      }

      case 'postal_code': {
        postcode = `${component.long_name}${postcode}`;
        address.postal_code = postcode;
        break;
      }

      case 'postal_code_suffix': {
        postcode = `${postcode}-${component.long_name}`;
        address.postal_code = postcode;
        break;
      }

      case 'locality':
        address.locality = component.long_name;
        break;

      case 'administrative_area_level_1': {
        address.administrative_area_level_1 = component.short_name;
        break;
      }

      case 'country':
        address.country = component.long_name;
        break;
    }
  }

  if (place.geometry) {
    address.latitude = place.geometry.location.lat().toString();
    address.longitude = place.geometry.location.lng().toString();
  }

  // additional fields available in google places results
  if (place.name) {
    address.name = place.name;
  }
  if (place.photos) {
    address.photos = place.photos;
  }
  if (place.place_id) {
    address.place_id = place.place_id;
  }

  address.formatted_address = [
    address.route,
    address.locality,
    address.postal_code,
    address.country,
  ]
    .filter((field) => field)
    .join(', ');

  return address;
}

export function useAutocomplete(
  options: google.maps.places.AutocompleteOptions,
  onPlaceSelected: (address: Address) => void
) {
  const ref = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (ref.current) {
      const autocomplete = new google.maps.places.Autocomplete(
        ref.current,
        options
      );
      // ref.current.focus();

      autocomplete.addListener('place_changed', () =>
        onPlaceSelected(fillInAddress(autocomplete))
      );
    }
  }, [onPlaceSelected, options]);

  return { ref };
}
