import { useContext } from 'react';
import { DonationContext } from './../context';

const useDonation = () => {
  const context = useContext(DonationContext);
  if (!context) {
    throw new Error(
      "Donation context provider not found, make sure you're using donation components correctly."
    );
  }
  return context;
};

export default useDonation;
