import { PaymentMethod } from '@stripe/stripe-js';
import { FC, useEffect } from 'react';
import { Button } from '../../lib';
import { errorHandler } from '../../utils';

interface DonationRecurringCheckoutProps {
  onSubmit: (paymentMethod: PaymentMethod) => void;
  loading: boolean;
}

const stripe = (window as any).Stripe(
  process.env.REACT_APP_STRIPE_PUBLIC_KEY as string
);

const DonationRecurringCheckout: FC<DonationRecurringCheckoutProps> = ({
  onSubmit,
  loading,
  children,
}) => {
  const classes =
    'px-8 py-2.5 w-full rounded-lg border-2 border-primary-500 transition-all bg-gray-100 disabled:cursor-not-allowed disabled:border-gray-300 disabled:text-gray-400 disabled:bg-gray-300 outline-none focus:ring-0';

  useEffect(() => {
    const elements = stripe.elements();

    if (!stripe || !elements) {
      return;
    }

    const cardNumberElement = elements.create('cardNumber', {
      placeholder: 'xxxx xxxx xxxx xxxx',
    });

    const cardExpiryElement = elements.create('cardExpiry', {
      placeholder: 'MM / YY',
    });

    const cardCvcElement = elements.create('cardCvc', {
      placeholder: '123',
    });

    cardExpiryElement.mount('#card-expiry-element');
    cardNumberElement.mount('#card-number-element');
    cardCvcElement.mount('#card-cvc-element');

    const form = document.getElementById('payment-form');
    form?.addEventListener('submit', (e) => {
      e.preventDefault();
      stripe
        .createPaymentMethod({
          type: 'card',
          card: cardNumberElement,
        })
        .then(({ paymentMethod, error }: any) => {
          if (paymentMethod) {
            onSubmit(paymentMethod);
          }
          if (error) {
            errorHandler(error.message as string);
          }
        });
    });
  }, [onSubmit]);

  return (
    <form id='payment-form' className='flex flex-col space-y-4'>
      <label>
        <span>Card number</span>
        <div id='card-number-element' className={classes} />
      </label>
      <label>
        <span>Expiry date</span>
        <div id='card-expiry-element' className={classes} />
      </label>
      <label>
        <span>CVC</span>
        <div id='card-cvc-element' className={classes} />
      </label>
      <div id='card-element-errors' role='alert' />
      <Button disabled={!stripe || loading} loading={loading}>
        {children || 'Subscribe'}
      </Button>
    </form>
  );
};

export default DonationRecurringCheckout;
