import VisaLineIcon from 'remixicon-react/VisaLineIcon';
import MastercardLineIcon from 'remixicon-react/MastercardLineIcon';
import BankCardLineIcon from 'remixicon-react/BankCardLineIcon';

export type CardBrand = 'visa' | 'mastercard';

interface SavedCardBrandProps {
  brand?: CardBrand;
}

const SavedCardBrand = ({ brand }: SavedCardBrandProps) => {
  switch (brand) {
    case 'visa':
      return <VisaLineIcon size={64} />;
    case 'mastercard':
      return <MastercardLineIcon size={64} />;
    default:
      return <BankCardLineIcon size={64} />;
  }
};

export default SavedCardBrand;
