import clsx from 'clsx';
import { FC, HTMLAttributes } from 'react';
import { View } from '.';

interface AvatarProps extends HTMLAttributes<HTMLDivElement> {
  size: Size;
  name: string;
  src?: string;
  thumbnail?: boolean;
}

const getInitials = (name: string): string =>
  name
    .split(' ')
    .map((part) => part[0])
    .join('')
    .toUpperCase();

const getSize = (size: Size, thumbnail: boolean) =>
  clsx({
    'w-12 h-12 text-xl': size === 'x-small',
    'w-16 h-16 text-xl': size === 'small',
    'w-[5.5rem] h-[5.5rem] text-2xl': size === 'medium',
    'w-32 h-32 text-3xl': size === 'large',
    'w-48 h-48 text-4xl': size === 'x-large',
    'ring-4 ring-white shadow-xl': thumbnail,
  });

const Avatar: FC<AvatarProps> = ({
  src,
  size = 'medium',
  name,
  className,
  thumbnail = false,
}) => (
  <View
    className={clsx(
      'justify-center items-center rounded-full font-bold bg-primary-200 overflow-hidden',
      className,
      getSize(size, thumbnail)
    )}
  >
    {src ? (
      <img src={src} alt='Avatar' className='object-cover h-full w-full' />
    ) : (
      getInitials(name)
    )}
  </View>
);

export default Avatar;
