import clsx from 'clsx';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import HeartFillIcon from 'remixicon-react/HeartFillIcon';
import usePermission from '../../hooks/usePermission';
import { Avatar, Button, Text, View } from '../../lib';
import { favorite, unfavorite } from '../../service';
import {
  currencyFormat,
  errorHandler,
  formatDate,
  getAssetUrl,
  successHandler,
} from '../../utils';

interface OrganizationCardProps {
  organization: User;
  refetch: () => void;
  createdAt: Date;
  className?: string;
  amount?: number;
  showDonation?: boolean;
  disabled?: boolean;
}

const OrganizationCard = ({
  organization,
  className,
  amount,
  createdAt,
  showDonation = false,
  disabled = false,
  refetch,
}: OrganizationCardProps) => {
  const [isMyFavorite, setIsMyFavorite] = useState(!!organization.isMyFavorite);
  const { canMakeOrganizationFavorite } = usePermission();

  const handleFavorite = () => {
    setIsMyFavorite((prev) => !prev);
    if (organization.isMyFavorite) {
      unfavorite(organization.id, 'organizations')
        .then((response) => {
          successHandler(response);
          refetch();
        })
        .catch((error) => {
          setIsMyFavorite(!!organization.isMyFavorite);
          errorHandler(error);
        });
    } else {
      favorite(organization.id, 'organizations')
        .then((response) => {
          successHandler(response);
          refetch();
        })
        .catch((error) => {
          setIsMyFavorite(!!organization.isMyFavorite);
          errorHandler(error);
        });
    }
  };

  return (
    <View
      className={clsx(
        'rounded-3xl border border-gray-300 p-4 bg-white shadow-md',
        className
      )}
    >
      <View row className='gap-2'>
        <Avatar
          src={getAssetUrl(organization.profileImage)}
          name={organization.name}
          size='x-small'
          thumbnail
        />
        <View className='flex-1'>
          <View row>
            <Link
              to={'/organizations/' + organization.id}
              className={clsx('flex-1', {
                'pointer-events-none': organization.deletedAt || disabled,
              })}
            >
              <Text type='h3'>
                {organization.name}{' '}
                {organization.deletedAt && (
                  <small className='font-medium tracking-wider text-xs text-gray-400 border border-gray-300 px-1 py-0.5 rounded-full ml-1'>
                    Archived
                  </small>
                )}
              </Text>
            </Link>
            {canMakeOrganizationFavorite &&
              organization.isMyFavorite !== undefined && (
                <Button
                  iconOnly
                  rounded='full'
                  small
                  className={clsx('ml-auto !border-transparent bg-gray-200', {
                    '!text-gray focus:text-gray': !isMyFavorite,
                    'text-error focus:text-error': isMyFavorite,
                  })}
                  onClick={handleFavorite}
                  intent='secondary'
                >
                  <HeartFillIcon size={18} />
                </Button>
              )}
          </View>
          <Text className='mt-0.5 text-xs line-clamp-2'>
            {organization.phone}
          </Text>
          {amount && showDonation && (
            <Text className='mt-0.5 text-xs'>
              <strong>Donated amount:</strong> {currencyFormat(amount)}
            </Text>
          )}
          {createdAt && showDonation && (
            <Text className='mt-0.5 text-xs'>{formatDate(createdAt)}</Text>
          )}
        </View>
      </View>
    </View>
  );
};

export default OrganizationCard;
