import { useContext } from 'react';
import { AuthContext } from '../context';
interface Permission {
  canDonate: boolean;
  canAddPaymentMethod: boolean;
  canAddBankInformation: boolean;
  canMakeCampaignFavorite: boolean;
  canMakeOrganizationFavorite: boolean;
  canControlCampaign: (campaignCreatedBy: ID) => boolean;
}

const usePermission = (): Permission => {
  const context = useContext(AuthContext);
  if (!context?.user) {
    throw new Error(
      "Auth context provider not found, make sure you're using auth components correctly."
    );
  }

  const { id, role } = context.user;

  const canDonate = role === 'individual';
  const canAddPaymentMethod = role === 'individual';
  const canAddBankInformation = role === 'organization';
  const canMakeCampaignFavorite = false;
  const canMakeOrganizationFavorite = true;
  const canControlCampaign = (campaignCreatedBy: ID) =>
    role === 'organization' && id === campaignCreatedBy;

  return {
    canDonate,
    canAddPaymentMethod,
    canAddBankInformation,
    canMakeCampaignFavorite,
    canMakeOrganizationFavorite,
    canControlCampaign,
  };
};

export default usePermission;
