export default function usePasswordCriteria(password: string = '') {
  const hasRequiredLength = password.length >= 8;
  const hasUppercaseCharacter = /^(?=.*[A-Z]).+$/.test(password);
  const hasLowercaseCharacter = /^(?=.*[a-z]).+$/.test(password);
  const hasNumericCharacter = /^(?=.*[0-9]).+$/.test(password);
  const hasSpecialCharacter =
    /^(?=.*[|\\/~^:,;?!&%$@*+"#'()-.<=>[\]_`{}]).+$/.test(password);

  const isValid =
    hasRequiredLength &&
    hasUppercaseCharacter &&
    hasLowercaseCharacter &&
    hasNumericCharacter &&
    hasSpecialCharacter;

  return {
    isValid,
    hasRequiredLength,
    hasUppercaseCharacter,
    hasLowercaseCharacter,
    hasNumericCharacter,
    hasSpecialCharacter,
  };
}
