import { useState } from 'react';
import { useQuery } from 'react-query';
import { Link, useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import { Button, Text, View } from '../../lib';
import Banner from '../profile/Banner';
import CampaignProgress from './CampaignProgress';
import {
  deleteCampaign,
  favorite,
  getCampaign,
  unfavorite,
} from '../../service';
import { errorHandler, getAssetUrl, successHandler } from '../../utils';
import CampaignSkeleton from './CampaignSkeleton';
import Dialog from '../common/Dialog';
import CampaignCreate from './CampaignCreate';
import useGo from '../../hooks/useGo';
import usePermission from '../../hooks/usePermission';

import More2FillIcon from 'remixicon-react/More2FillIcon';
import CloseCircleFillIcon from 'remixicon-react/CloseCircleFillIcon';

const CampaignDetail = () => {
  const [deleting, setDeleting] = useState(false);
  const params = useParams();
  const navigate = useNavigate();
  const goBack = useGo(-1);
  const { canDonate, canControlCampaign, canMakeCampaignFavorite } =
    usePermission();

  const { isLoading, data, refetch } = useQuery(
    ['campaign', params.id],
    () => getCampaign(Number(params.id)),
    {
      retry: false,
      onError(error: ErrorResponse) {
        errorHandler(error);
        goBack();
      },
    }
  );

  const handleFavorite = () => {
    if (!data) return;
    if (data.isMyFavorite) {
      unfavorite(data.id, 'campaigns')
        .then(() => refetch())
        .catch(errorHandler);
    } else {
      favorite(data.id, 'campaigns')
        .then(() => refetch())
        .catch(errorHandler);
    }
  };

  const handleDeleteCampaign = () => {
    if (!data) return;
    setDeleting(true);
    deleteCampaign(data.id)
      .then((response) => {
        successHandler(response);
        navigate('/', { replace: true });
      })
      .catch((error) => {
        errorHandler(error.data);
      })
      .finally(() => {
        setDeleting(false);
      });
  };

  return (
    <View container className='py-5' content>
      {isLoading && <CampaignSkeleton action />}
      {data && (
        <>
          <Banner
            src={getAssetUrl(data.thumbnailImage)}
            params={{
              title: data.title,
              type: 'campaign',
              id: Number(params.id),
            }}
            onFavorite={canMakeCampaignFavorite ? handleFavorite : undefined}
            favorite={canMakeCampaignFavorite ? !!data.isMyFavorite : undefined}
          />
          <View row className='items-center justify-between mt-6 mb-4'>
            <Link to={'/organizations/' + data.createdBy}>
              <Text type='h2'>{data.title}</Text>
            </Link>
            {canControlCampaign(data.createdBy) && (
              <Dialog
                activator={(setOpen) => (
                  <Button
                    iconOnly
                    small
                    rounded='full'
                    intent='secondary'
                    className='border-transparent focus:border-transparent focus:ring-0 hover:border-transparent'
                    onClick={() => setOpen(true)}
                  >
                    <More2FillIcon size={18} />
                  </Button>
                )}
              >
                {(setOpen) => (
                  <View className='m-6 pb-6 min-w-[280px] lg:min-w-[448px]'>
                    <View row className='items-center justify-between'>
                      <Text type='h1'>Edit Campaign</Text>
                      <CloseCircleFillIcon
                        size={24}
                        onClick={() => setOpen(false)}
                      />
                    </View>
                    <Dialog
                      activator={(setOpen) => (
                        <Button
                          intent='secondary'
                          className='mt-12 mb-5'
                          onClick={() => setOpen(true)}
                        >
                          Edit Campaign
                        </Button>
                      )}
                    >
                      {(setEditOpen) => (
                        <View className='m-6 min-w-[280px] lg:min-w-[448px]'>
                          <CampaignCreate
                            initialData={{
                              id: data.id,
                              title: data.title,
                              description: data.description,
                              goalAmount: data.goalAmount.toString(),
                              thumbnailImage: getAssetUrl(data.thumbnailImage),
                            }}
                            onClose={() => {
                              refetch();
                              setEditOpen(false);
                              setOpen(false);
                            }}
                          />
                        </View>
                      )}
                    </Dialog>
                    <Dialog
                      activator={(setOpen) => (
                        <Button intent='error' onClick={() => setOpen(true)}>
                          Delete Campaign
                        </Button>
                      )}
                      persistent={deleting}
                    >
                      {(setOpen) => (
                        <View className='m-6 pb-6 min-w-[280px] lg:min-w-[448px]'>
                          <View className='items-center justify-between'>
                            <Text type='h1'>Are you sure?</Text>
                            <View row className='w-full mt-6 space-x-3'>
                              <Button
                                className='flex-1'
                                onClick={() => setOpen(false)}
                                disabled={deleting}
                              >
                                Cancel
                              </Button>
                              <Button
                                className='flex-1'
                                intent='error'
                                onClick={handleDeleteCampaign}
                                loading={deleting}
                                disabled={deleting}
                              >
                                Delete
                              </Button>
                            </View>
                          </View>
                        </View>
                      )}
                    </Dialog>
                  </View>
                )}
              </Dialog>
            )}
          </View>
          <Button
            tag={Link}
            className='mb-8'
            to={'/organizations/' + data.createdBy}
            intent='secondary'
          >
            Show Profile
          </Button>
          <CampaignProgress
            raised={Number(data.raisedAmount)}
            goal={Number(data.goalAmount)}
            fluid
          />
          <View className='flex-1 p-4 my-6 bg-white border-2 border-gray-300 rounded-lg'>
            <Text className='small'>{data.description}</Text>
          </View>
          {canDonate && Number(data.raisedAmount) < Number(data.goalAmount) && (
            <Button
              tag={Link}
              to={'/campaigns/' + params.id + '/donation/campaign'}
            >
              Donate
            </Button>
          )}
        </>
      )}
    </View>
  );
};

export default CampaignDetail;
