import { Dispatch, SetStateAction } from 'react';
import { Text, View } from '../../lib';
import OrganizationCard from './OrganizationCard';
import Pagination from '../common/Pagination';
import LoaderSkeleton from '../common/LoaderSkeleton';

import globeImage from '../../assets/img/illustrations/globe.png';
import searchImage from '../../assets/img/illustrations/search.png';
interface OrganizationsProps {
  loading: boolean;
  refetch: () => void;
  setPage: Dispatch<SetStateAction<number>>;
  organizations?: Organizations;
  action?: boolean;
  showDonation?: boolean;
  search?: boolean;
}

const Organizations = ({
  loading,
  organizations,
  refetch,
  setPage,
  action = false,
  showDonation = false,
  search = false,
}: OrganizationsProps) => {
  const loadMore = () => {
    if (organizations?.meta) {
      setPage(organizations.meta.currentPage + 1);
    }
  };

  return (
    <>
      {loading && !organizations ? (
        <LoaderSkeleton action={action} />
      ) : organizations?.items.length ? (
        <>
          <div className='grid gap-4 lg:grid-cols-2 2xl:grid-cols-3'>
            {organizations.items.map((organization) => (
              <OrganizationCard
                organization={organization}
                refetch={refetch}
                amount={121}
                createdAt={new Date()}
                showDonation={showDonation}
                key={organization.id}
              />
            ))}
          </div>
          {organizations?.meta && (
            <Pagination
              loading={loading}
              meta={organizations.meta}
              loadMore={loadMore}
            />
          )}
        </>
      ) : (
        <View container className='mt-5'>
          <View className='flex-1 text-center justify-evenly'>
            <img
              src={search ? searchImage : globeImage}
              alt='Icon'
              className='mx-auto w-52'
            />
            <div className='mt-5'>
              <Text type='h2' className='text-2xl'>
                {search ? 'No results!' : 'No organizations'}
              </Text>
              <Text className='mt-3'>
                {search
                  ? 'Please try Searching with another term.'
                  : "You don't have any organizations yet."}
              </Text>
            </div>
          </View>
        </View>
      )}
    </>
  );
};

export default Organizations;
