import { ChangeEvent, useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import clsx from 'clsx';
import { Elements } from '@stripe/react-stripe-js';
import { Button, Input, Select, Text, View } from '../../lib';
import { addDays, currencyFormat, errorHandler } from '../../utils';
import BackButton from '../common/BackButton';
import TouchInput from '../common/TouchInput';
import DonationCheckout from './DonationCheckout';
import useDonation from '../../hooks/useDonation';
import useAuth from '../../hooks/useAuth';
import DonationConfirmation from './DonationConfirmation';
import Tab, { TabItem } from '../common/Tab';
import {
  createStripeIntent,
  getProfile,
  recurringSubscription,
  updatePaymentMethod,
} from '../../service';
import AddPaymentMethod from '../common/AddPaymentMethod';
import { PaymentMethod } from '@stripe/stripe-js';
import DatePicker from 'react-datepicker';

const tabs: TabItem[] = [
  { value: 'one-time', content: 'One time' },
  { value: 'recurring', content: 'Recurring' },
];

const Donation = () => {
  const [type, setType] = useState(tabs[0]);
  const [step, setStep] = useState(1);
  const [loading, setLoading] = useState(false);
  const [donation, setDonation] = useState('20');
  const [customDonation, setCustomDonation] = useState('');
  const [interval, setInterval] = useState<RecurringInterval>('biweek');
  const [startDate, setStartDate] = useState<Date | null>(null);
  const [endDate, setEndDate] = useState<Date | null>(null);

  const { stripePromise, stripeIntent, setStripeIntent } = useDonation();
  const { user, setUser } = useAuth();
  const params = useParams();
  const navigate = useNavigate();

  const now = new Date();
  const tomorrow = new Date(now.getTime() + now.getTimezoneOffset() * 60000);
  tomorrow.setDate(new Date().getDate() + 1);
  const endDateMinLimit = tomorrow;
  if (startDate) {
    const startDateString = new Date(startDate);
    const utcStartDate = new Date(
      startDateString.getTime() + startDateString.getTimezoneOffset() * 60000
    );
    endDateMinLimit.setDate(new Date(utcStartDate).getDate() + 1);
  }

  const handleDonation = ({
    target: { value },
  }: ChangeEvent<HTMLInputElement>) => {
    setCustomDonation('');
    setDonation(value);
  };

  const handleCustomDonation = ({
    target: { value },
  }: ChangeEvent<HTMLInputElement>) => {
    setDonation('0');
    setCustomDonation(value);
  };

  const handleOneTimeDonation = () => {
    setLoading(true);

    createStripeIntent({
      donationToType: params.donationType as DonationType,
      donationToId: Number(params.id),
      amount: Number(customDonation || donation),
    })
      .then((response) => {
        setStripeIntent(response);
        setStep(2);
      })
      .catch(errorHandler)
      .finally(() => {
        setLoading(false);
      });
  };

  const handleRecurringDonation = (paymentMethod: PaymentMethod) => {
    updatePaymentMethod({ paymentMethodId: paymentMethod!.id })
      .then(() => {
        getProfile()
          .then((user) => {
            setUser(user);
            handleRecurringSubscription();
          })
          .catch(errorHandler);
      })
      .catch(errorHandler);
  };

  const handleRecurringSubscription = () => {
    setLoading(true);
    const startDateString = new Date(startDate || '');
    const utcStartDate = new Date(
      startDateString.getTime() + startDateString.getTimezoneOffset() * 60000
    );

    const endDateString = new Date(endDate || '');
    const utcEndDate = new Date(
      endDateString.getTime() + endDateString.getTimezoneOffset() * 60000
    );

    recurringSubscription({
      amount: Number(customDonation || donation),
      subscribedToId: Number(params.id),
      interval,
      startDate: startDate ? utcStartDate : '',
      endDate: endDate ? utcEndDate : '',
    })
      .then(() => {
        navigate('/success', { replace: true });
      })
      .catch(errorHandler)
      .finally(() => {
        setLoading(false);
      });
  };

  const isContinueDisabled =
    !(customDonation || donation) || type.value === 'recurring'
      ? !interval
      : false;

  useEffect(() => {
    const startTime = new Date(startDate || '').getTime();
    const endTime = new Date(endDate || '').getTime();

    if (endTime <= startTime) {
      setEndDate(null);
    }
  }, [endDate, startDate]);

  return (
    <View content className='py-6 space-y-8 container-mini'>
      <View className='items-center space-x-4' row>
        <BackButton onClick={step === 2 ? () => setStep(1) : undefined} />
        <Text type='h1'>{stripeIntent ? 'Payment details' : 'Donation'}</Text>
      </View>
      {step === 1 && (
        <>
          {params.donationType === 'organization' && (
            <Tab tabs={tabs} currentTab={type} onTabChange={setType} />
          )}
          <Text type='h3'>Choose donation amount</Text>
          <form className='flex-1 space-y-5'>
            <View
              row={type.value === 'recurring'}
              className={clsx({
                'space-y-5': type.value === 'one-time',
                'space-x-2': type.value === 'recurring',
              })}
            >
              {['20', '50', '100'].map((amount) => (
                <label
                  className={clsx(
                    'flex-1 border rounded-lg font-bold text-xl flex p-5 items-center justify-center transition cursor-pointer',
                    {
                      'text-white border-primary bg-primary':
                        donation === amount,
                      'border-primary-300 bg-gray-200': donation !== amount,
                    }
                  )}
                  key={amount}
                >
                  {currencyFormat(Number(amount), { decimal: 0 })}
                  <input
                    name='donation'
                    type='radio'
                    value={amount}
                    checked={donation === amount}
                    onChange={handleDonation}
                    className='hidden'
                  />
                </label>
              ))}
            </View>
            <View
              className={clsx(
                'border rounded-lg font-bold text-xl flex p-5 items-center justify-center transition',
                {
                  'text-white border-primary bg-primary': customDonation,
                  'border-primary-300 bg-gray-200': !customDonation,
                }
              )}
              row
            >
              <TouchInput
                type='number'
                name='donation'
                value={customDonation}
                onChange={handleCustomDonation}
                formatter={(value) => currencyFormat(Number(value))}
                placeholder='Other amount'
              />
            </View>

            {type.value === 'recurring' && (
              <View className='space-y-5'>
                <Text type='h3'>Select recurring pattern</Text>
                <Select
                  label='Interval'
                  options={[
                    { value: 'biweek', text: 'Bi-Weekly' },
                    { value: 'month', text: 'Month' },
                  ]}
                  value={interval}
                  onChange={({ target }: ChangeEvent<HTMLSelectElement>) =>
                    setInterval(target.value as RecurringInterval)
                  }
                />
                <DatePicker
                  selected={startDate}
                  onChange={setStartDate}
                  minDate={tomorrow}
                  placeholderText='Start date (optional)'
                  customInput={<Input />}
                />
                <DatePicker
                  selected={endDate}
                  onChange={setEndDate}
                  minDate={addDays(startDate || tomorrow, 1)}
                  placeholderText='End date (optional)'
                  customInput={<Input />}
                />
              </View>
            )}
          </form>

          <Button
            className='mt-8'
            onClick={
              type.value === 'recurring'
                ? () => setStep(2)
                : handleOneTimeDonation
            }
            disabled={loading || isContinueDisabled}
            loading={loading}
            block
          >
            Continue
          </Button>
        </>
      )}

      {step === 2 &&
        type.value === 'one-time' &&
        (user.stripePaymentMethodId ? (
          <DonationConfirmation stripeIntent={stripeIntent} />
        ) : stripeIntent ? (
          <Elements
            stripe={stripePromise}
            options={{
              clientSecret: stripeIntent.client_secret,
            }}
          >
            <DonationCheckout />
          </Elements>
        ) : null)}

      {step === 2 &&
        type.value === 'recurring' &&
        (user.stripePaymentMethodId ? (
          <DonationConfirmation
            loading={loading}
            amount={Number(customDonation || donation)}
            onSubmit={handleRecurringSubscription}
          />
        ) : (
          <View container>
            <Elements stripe={stripePromise}>
              <AddPaymentMethod
                onSubmit={handleRecurringDonation}
                loading={loading}
              />
            </Elements>
          </View>
        ))}
    </View>
  );
};

export default Donation;
