import { Text, View } from '../../lib';
import CampaignCard from './CampaignCard';
import { Dispatch, SetStateAction } from 'react';
import Pagination from '../common/Pagination';
import LoaderSkeleton from '../common/LoaderSkeleton';

import image from '../../assets/img/illustrations/globe.png';

interface CampaignsProps {
  loading: boolean;
  refetch: () => void;
  setPage: Dispatch<SetStateAction<number>>;
  campaigns?: Campaigns;
  action?: boolean;
}

const Campaigns = ({
  loading,
  campaigns,
  refetch,
  setPage,
  action = false,
}: CampaignsProps) => {
  const loadMore = () => {
    if (campaigns?.meta) {
      setPage(campaigns.meta.currentPage + 1);
    }
  };

  return (
    <>
      {loading && !campaigns ? (
        <LoaderSkeleton action={action} />
      ) : campaigns?.items.length ? (
        <>
          <div className='grid gap-4 lg:grid-cols-2 2xl:grid-cols-3'>
            {campaigns.items.map((campaign) => (
              <CampaignCard
                campaign={campaign}
                action={action}
                refetch={refetch}
                key={campaign.id}
              />
            ))}
          </div>
          {campaigns?.meta && (
            <Pagination
              loading={loading}
              meta={campaigns.meta}
              loadMore={loadMore}
            />
          )}
        </>
      ) : (
        <View container className='mt-5'>
          <View className='flex-1 text-center justify-evenly'>
            <img src={image} alt='Icon' className='mx-auto w-52' />
            <div className='mt-5'>
              <Text type='h2' className='text-2xl'>
                No Campaigns
              </Text>
              {/* <Text className='mt-3'>You don’t have any campaigns yet.</Text> */}
            </div>
          </View>
        </View>
      )}
    </>
  );
};

export default Campaigns;
