import clsx from 'clsx';
import { Text, View } from '../../lib';
import { currencyFormat } from '../../utils';
import Progress from '../common/Progress';

interface CampaignProgressProps {
  raised: number;
  goal: number;
  fluid?: boolean;
}

const CampaignProgress = ({
  raised,
  goal,
  fluid = false,
}: CampaignProgressProps) => {
  return (
    <View
      className={clsx('gap-3', {
        'flex-1': !fluid,
      })}
    >
      <View row className='justify-between gap-2'>
        <Text type={fluid ? 'h5' : 'h6'} className='font-bold text-secondary'>
          Raised so far{' '}
          <span className='text-primary'>{currencyFormat(raised)}</span>
        </Text>
        <Text type={fluid ? 'h5' : 'h6'} className='text-secondary'>
          Goal <strong className='text-primary'>{currencyFormat(goal)}</strong>
        </Text>
      </View>
      <Progress color='success' progress={raised / goal} />
    </View>
  );
};

export default CampaignProgress;
