import { ChangeEvent, FormEvent, useState } from 'react';
import {
  useStripe,
  useElements,
  PaymentElement,
} from '@stripe/react-stripe-js';
import { Button, Switch } from '../../lib';
import { errorHandler, isWeb } from '../../utils';
import ApiService from '../../service';
import useAuth from '../../hooks/useAuth';

const DonationCheckout = () => {
  const [loading, setLoading] = useState(false);
  const [savePaymentMethod, setSavePaymentMethod] = useState(false);
  const { setUser } = useAuth();

  const stripe = useStripe();
  const elements = useElements();

  const handleSavePaymentMethod = ({
    target: { checked },
  }: ChangeEvent<HTMLInputElement>) => {
    setSavePaymentMethod(checked);
  };

  const handleConfirmPayment = (e: FormEvent) => {
    e.preventDefault();

    if (!stripe || !elements) {
      return;
    }

    setLoading(true);
    // const returnUrl = `${window.location.protocol}//${window.location.pathname}#/success`;
    const returnUrl = isWeb
      ? window.location.origin
      : process.env.REACT_APP_HOST_URL + '/#/success';

    stripe
      .confirmPayment({
        elements,
        confirmParams: {
          return_url: returnUrl,
          save_payment_method: savePaymentMethod,
        },
      })
      .then((response) => {
        if (savePaymentMethod) {
          ApiService.getProfile().then(setUser).catch(errorHandler);
        }
        if (response.error) {
          errorHandler(response.error as unknown as ErrorResponse);
        }
      })
      .catch(errorHandler)
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <form onSubmit={handleConfirmPayment} className='flex flex-col flex-1'>
      <PaymentElement className='flex-1' />
      <label className='cursor-pointer'>
        <Switch
          checked={savePaymentMethod}
          onChange={handleSavePaymentMethod}
        />
        <span className='ml-3'>Save this payment method</span>
      </label>

      <Button
        className='mt-8'
        type='submit'
        disabled={loading}
        loading={loading}
        block
      >
        Confirm payment
      </Button>
    </form>
  );
};

export default DonationCheckout;
