import { ChangeEvent, useState } from 'react';
import { useQuery } from 'react-query';
import Banner from '../components/common/Banner';
import Organizations from '../components/organization/Organizations';
import useDebounce from '../hooks/useDebounce';
import { Container, Input, Text, View } from '../lib';
import { getOrganizations } from '../service';

const Search = () => {
  const [query, setQuery] = useState('');
  const [page, setPage] = useState(1);
  const [organizations, setOrganizations] = useState<Organizations>();
  const debouncedSearch = useDebounce(query, 500);
  const { isLoading, refetch } = useQuery(
    ['organizations', 'search', page, debouncedSearch],
    () =>
      getOrganizations({
        search: debouncedSearch,
        page,
      }),
    {
      enabled: !!debouncedSearch,
      onSuccess({ data }) {
        setOrganizations(data);
      },
    }
  );

  const handleSearch = ({ target }: ChangeEvent<HTMLInputElement>) => {
    setQuery(target.value);
  };

  return (
    <View content>
      <Banner sticky>
        <Container className='py-5 space-y-5'>
          <Text type='h2' className='sticky top-0 z-10 text-2xl'>
            Search
          </Text>
          <Input
            placeholder='Search for Mosque or City...'
            value={query}
            onChange={handleSearch}
          />
        </Container>
      </Banner>
      {debouncedSearch && (
        <View container className='flex-1'>
          <div className='py-5 space-y-5'>
            <Organizations
              loading={isLoading}
              organizations={organizations}
              refetch={refetch}
              setPage={setPage}
              search
            />
          </div>
        </View>
      )}
    </View>
  );
};

export default Search;
