import { FC, HTMLAttributes, ReactNode } from 'react';
import { Link, useMatch, useResolvedPath, LinkProps } from 'react-router-dom';
import clsx from 'clsx';
import { View } from '.';
import Logo from '../components/common/Logo';
import HomeSmileFillIcon from 'remixicon-react/HomeSmileFillIcon';
import HomeSmileLineIcon from 'remixicon-react/HomeSmileLineIcon';
import Search2FillIcon from 'remixicon-react/Search2FillIcon';
import Search2LineIcon from 'remixicon-react/Search2LineIcon';
import HeartFillIcon from 'remixicon-react/HeartFillIcon';
import HeartLineIcon from 'remixicon-react/HeartLineIcon';
import UserFillIcon from 'remixicon-react/UserFillIcon';
import UserLineIcon from 'remixicon-react/UserLineIcon';
import Settings3FillIcon from 'remixicon-react/Settings3FillIcon';
import Settings3LineIcon from 'remixicon-react/Settings3LineIcon';

// import logo from '../assets/img/logo.png';

interface ToolbarProps extends HTMLAttributes<HTMLDivElement> {}

interface CustomLinkProps extends LinkProps {
  activeIcon: ReactNode;
}

const CustomLink: FC<CustomLinkProps> = ({
  to,
  className,
  activeIcon,
  children,
  ...props
}) => {
  let resolved = useResolvedPath(to);
  let match = useMatch({ path: resolved.pathname, end: true });

  return (
    <Link
      className={clsx(
        'flex items-center justify-center flex-1 font-medium',
        className,
        match ? 'text-primary' : 'text-secondary'
      )}
      to={to}
      {...props}
    >
      {match ? activeIcon : children}
    </Link>
  );
};

const Toolbar: FC<ToolbarProps> = ({ className }) => {
  return (
    <View
      className={clsx(
        'fixed left-0 right-0 h-20 bg-white z-30 items-center',
        className
      )}
      row
    >
      <Link to='/' className='hidden w-48 mx-4 cursor-pointer lg:block'>
        <Logo />
      </Link>

      <CustomLink
        activeIcon={
          <div className='flex px-6 py-3 rounded-lg lg:bg-primary-100'>
            <HomeSmileFillIcon />
            <span className='hidden ml-2 lg:block'>Home</span>
          </div>
        }
        to='/'
      >
        <HomeSmileLineIcon />
        <span className='hidden ml-2 lg:block'>Home</span>
      </CustomLink>
      <CustomLink
        activeIcon={
          <div className='flex px-6 py-3 rounded-lg lg:bg-primary-100'>
            <Search2FillIcon />
            <span className='hidden ml-2 lg:block'>Search</span>
          </div>
        }
        to='/search'
      >
        <Search2LineIcon />
        <span className='hidden ml-2 lg:block'>Search</span>
      </CustomLink>
      <CustomLink
        activeIcon={
          <div className='flex px-6 py-3 rounded-lg lg:bg-primary-100'>
            <HeartFillIcon />
            <span className='hidden ml-2 lg:block'>Favorites</span>
          </div>
        }
        to='/favorites'
      >
        <HeartLineIcon />
        <span className='hidden ml-2 lg:block'>Favorites</span>
      </CustomLink>
      <CustomLink
        activeIcon={
          <div className='flex px-6 py-3 rounded-lg lg:bg-primary-100'>
            <UserFillIcon />
            <span className='hidden ml-2 lg:block'>My Profile</span>
          </div>
        }
        to='/profile'
      >
        <UserLineIcon />
        <span className='hidden ml-2 lg:block'>My Profile</span>
      </CustomLink>
      <CustomLink
        activeIcon={
          <div className='flex px-6 py-3 rounded-lg lg:bg-primary-100'>
            <Settings3FillIcon />
            <span className='hidden ml-2 lg:block'>Settings</span>
          </div>
        }
        to='/settings'
      >
        <Settings3LineIcon />
        <span className='hidden ml-2 lg:block'>Settings</span>
      </CustomLink>
    </View>
  );
};

export default Toolbar;
