import { Dispatch, SetStateAction, useState } from 'react';
import { Button, Text, View } from '../../lib';
import BackButton from '../../components/common/BackButton';
import SavedCard from '../../components/common/cards/SavedCard';
import {
  deletePaymentMethod,
  getProfile,
  getRecurringPayments,
  updatePaymentMethod,
} from '../../service';
import { useQuery } from 'react-query';
import RecurringPayment from '../../components/settings/RecurringPayment';
import Banner from '../../components/common/Banner';
import DeleteBin5FillIcon from 'remixicon-react/DeleteBin5FillIcon';
import Dialog from '../../components/common/Dialog';
import AddPaymentMethod from '../../components/common/AddPaymentMethod';
import { Elements } from '@stripe/react-stripe-js';
import { loadStripe, PaymentMethod } from '@stripe/stripe-js';
import { errorHandler, successHandler } from '../../utils';
import useAuth from '../../hooks/useAuth';
import CloseCircleFillIcon from 'remixicon-react/CloseCircleFillIcon';
import Pagination from '../../components/common/Pagination';
import LoaderSkeleton from '../../components/common/LoaderSkeleton';

import image from '../../assets/img/illustrations/error.png';

const stripePublicKey = process.env.REACT_APP_STRIPE_PUBLIC_KEY!;
const stripePromise = loadStripe(stripePublicKey);

const Payments = () => {
  const [loading, setLoading] = useState(false);
  const [recurringPayments, setRecurringPayments] =
    useState<RecurringPayments>();
  const [page, setPage] = useState(1);
  const { user, setUser } = useAuth();
  const { isLoading, refetch } = useQuery(
    ['recurring-payments', page],
    () => getRecurringPayments({ page }),
    {
      onSuccess(data) {
        setRecurringPayments((prevData) =>
          prevData
            ? {
                items: [...prevData.items, ...data.items],
                meta: data.meta,
              }
            : data
        );
      },
    }
  );

  const loadMore = () => {
    if (recurringPayments?.meta) {
      setPage(recurringPayments.meta.currentPage + 1);
    }
  };

  const addPaymentMethod = (
    paymentMethod: PaymentMethod,
    setOpen: Dispatch<SetStateAction<boolean>>
  ) => {
    setLoading(true);
    updatePaymentMethod({ paymentMethodId: paymentMethod!.id })
      .then((response) => {
        successHandler(response);
        getProfile()
          .then(setUser)
          .catch(errorHandler)
          .finally(() => setLoading(false));
      })
      .catch((error) => {
        setLoading(false);
        errorHandler(error);
      })
      .finally(() => {
        setOpen(false);
      });
  };

  const handleDeletePaymentMethod = (
    setOpen: Dispatch<SetStateAction<boolean>>
  ) => {
    setLoading(true);
    deletePaymentMethod()
      .then((response) => {
        successHandler(response);
        getProfile()
          .then(setUser)
          .catch(errorHandler)
          .finally(() => setLoading(false));
      })
      .catch(errorHandler)
      .finally(() => {
        setLoading(false);
        setOpen(false);
      });
  };

  return (
    <View content>
      <Banner sticky className='z-10 pt-12 pb-4'>
        <View container row className='items-center space-x-4'>
          <BackButton />
          <Text type='h1'>Payment methods</Text>
        </View>
      </Banner>

      <View container className='mt-8'>
        {user.stripePaymentMethodId && (
          <>
            <Text type='h2' className='mb-4'>
              Card details
            </Text>
            <SavedCard />
          </>
        )}
        <View row className='justify-between mt-4'>
          <Dialog
            activator={(setOpen) => (
              <Button onClick={() => setOpen(true)}>
                {user.stripePaymentMethodId ? 'Edit' : 'Add'} card
              </Button>
            )}
          >
            {(setOpen) => (
              <Elements stripe={stripePromise}>
                <View container className='py-6 lg:min-w-[448px] lg:px-6'>
                  <Text type='h1' className='mb-4'>
                    Payment details
                  </Text>
                  <AddPaymentMethod
                    onSubmit={(e) => addPaymentMethod(e, setOpen)}
                    loading={loading}
                  >
                    Save changes
                  </AddPaymentMethod>
                </View>
              </Elements>
            )}
          </Dialog>
          {user.stripePaymentMethodId && (
            <Dialog
              activator={(setOpen) => (
                <Button
                  intent='error'
                  className='px-3 bg-red-100 border-red-100 text-error'
                  onClick={() => setOpen(true)}
                >
                  <DeleteBin5FillIcon />
                </Button>
              )}
              persistent={loading}
            >
              {(setOpen) => (
                <View className='m-6 pb-6 min-w-[280px] lg:min-w-[448px]'>
                  <View className='items-center justify-between' row>
                    <Text type='h1'>Delete card</Text>
                    <CloseCircleFillIcon
                      size={24}
                      onClick={() => setOpen(false)}
                    />
                  </View>
                  <View className='mt-8 space-y-5'>
                    {!user.canDeletePaymentMethod && (
                      <Text>
                        You have active recurring payments, this action isn't
                        allowed!
                      </Text>
                    )}
                    <Button
                      intent='error'
                      onClick={() => handleDeletePaymentMethod(setOpen)}
                      loading={loading}
                      disabled={loading || !user.canDeletePaymentMethod}
                    >
                      Yes, delete it
                    </Button>
                    <Button
                      intent='secondary'
                      onClick={() => setOpen(false)}
                      disabled={loading}
                    >
                      No, keep it
                    </Button>
                  </View>
                </View>
              )}
            </Dialog>
          )}
        </View>
      </View>

      <View container className='mt-8 mb-4 space-y-4'>
        <Text type='h2'>Recurring Payments</Text>
        {isLoading && !recurringPayments ? (
          <LoaderSkeleton action />
        ) : recurringPayments?.items.length ? (
          <>
            {recurringPayments?.items.map((recurringPayment) => (
              <RecurringPayment
                recurringPayment={recurringPayment}
                refetchRecurringPayments={refetch}
                key={recurringPayment.id}
              />
            ))}
            {recurringPayments?.meta && (
              <Pagination
                loading={isLoading}
                meta={recurringPayments.meta}
                loadMore={loadMore}
              />
            )}
          </>
        ) : (
          <View className='flex-1 text-center justify-evenly'>
            <img src={image} alt='Icon' className='mx-auto w-52' />
            <div className='mt-5'>
              <Text type='h2' className='mb-3 text-2xl'>
                No recurring donations!
              </Text>
              <Text>You don't have any recurring donations yet.</Text>
            </div>
          </View>
        )}
      </View>
    </View>
  );
};

export default Payments;
