import { forwardRef, HTMLAttributes } from 'react';
import clsx from 'clsx';
import { Text } from '.';

export interface InputProps extends HTMLAttributes<HTMLInputElement> {
  label?: string;
  name?: string;
  disabled?: boolean;
  value?: string;
  type?:
    | 'email'
    | 'hidden'
    | 'number'
    | 'password'
    | 'submit'
    | 'tel'
    | 'text'
    | 'url'
    | 'date'
    | 'datetime';
  success?: string;
  error?: string;
  alert?: string;
  autoComplete?: 'off';
  min?: string | number;
  max?: string | number;
}

const Input = forwardRef<HTMLInputElement | null, InputProps>(
  (
    {
      label,
      type = 'text',
      className,
      disabled,
      value,
      success,
      error,
      alert,
      autoComplete,
      ...rest
    },
    ref
  ) => (
    <div>
      <label>
        {label && <Text type='h5'>{label}</Text>}
        <input
          ref={ref}
          type={type}
          className={clsx(
            'px-8 py-2.5 w-full rounded-lg border-2 transition-all bg-gray-100 disabled:cursor-not-allowed disabled:border-gray-300 disabled:text-gray-400 disabled:bg-gray-300 outline-none focus:ring-0',
            className,

            {
              'border-primary-300 focus:border-primary caret-primary':
                !alert && !error,
              'border-error focus:border-error caret-error': error,
              'border-alert focus:border-alert caret-alert': alert,
              'border-success focus:border-success caret-success': success,
              disabled: disabled,
            }
          )}
          value={value}
          disabled={disabled}
          autoComplete={autoComplete}
          {...rest}
        />
      </label>
      {error && <Text className='text-error'>{error}</Text>}
      {alert && <Text className='text-alert'>{alert}</Text>}
      {success && <Text className='text-success'>{success}</Text>}
    </div>
  )
);

export default Input;
