import clsx from 'clsx';
import { FC, HTMLAttributes, useState } from 'react';
import { Link } from 'react-router-dom';
import HeartFillIcon from 'remixicon-react/HeartFillIcon';
import usePermission from '../../hooks/usePermission';
import { Avatar, Button, Text, View } from '../../lib';
import { favorite, unfavorite } from '../../service';
import {
  currencyFormat,
  errorHandler,
  formatDate,
  getAssetUrl,
  successHandler,
} from '../../utils';
import CampaignProgress from './CampaignProgress';

interface CampaignCardProps extends HTMLAttributes<HTMLDivElement> {
  campaign: Campaign;
  refetch: () => void;
  action?: boolean;
  amount?: number;
  createdAt?: Date;
  disabled?: boolean;
}

const CampaignCard: FC<CampaignCardProps> = ({
  campaign,
  refetch,
  action = false,
  className = '',
  amount,
  createdAt,
  disabled = false,
}) => {
  const [isMyFavorite, setIsMyFavorite] = useState(!!campaign.isMyFavorite);
  const { canDonate, canMakeCampaignFavorite } = usePermission();

  const handleFavorite = () => {
    setIsMyFavorite((prev) => !prev);
    if (campaign.isMyFavorite) {
      unfavorite(campaign.id, 'campaigns')
        .then((response) => {
          successHandler(response);
          refetch();
        })
        .catch((error) => {
          setIsMyFavorite(!!campaign.isMyFavorite);
          errorHandler(error);
        });
    } else {
      favorite(campaign.id, 'campaigns')
        .then((response) => {
          successHandler(response);
          refetch();
        })
        .catch((error) => {
          setIsMyFavorite(!!campaign.isMyFavorite);
          errorHandler(error);
        });
    }
  };

  return (
    <View
      className={clsx(
        'rounded-3xl border border-gray-300 p-4 bg-white shadow-md',
        className
      )}
    >
      <View row className='gap-2'>
        <Avatar
          src={getAssetUrl(campaign.thumbnailImage)}
          name={campaign.title}
          size='x-small'
          thumbnail
        />
        <View className='flex-1' row>
          <Link
            to={'/campaigns/' + campaign.id}
            className={clsx('flex-1', {
              'pointer-events-none': campaign.deletedAt || disabled,
            })}
          >
            <Text type='h3'>
              {campaign.title}{' '}
              {campaign.deletedAt && (
                <small className='font-medium tracking-wider text-xs text-gray-400 border border-gray-300 px-1 py-0.5 rounded-full ml-1'>
                  Archived
                </small>
              )}
            </Text>
            <Text className='mt-0.5 text-xs line-clamp-2'>
              {campaign.description}
            </Text>
            {amount && (
              <Text className='mt-0.5 text-xs'>
                <strong>Donated amount:</strong> {currencyFormat(amount)}
              </Text>
            )}
            {createdAt && (
              <Text className='mt-0.5 text-xs'>{formatDate(createdAt)}</Text>
            )}
          </Link>
          {canMakeCampaignFavorite && campaign.isMyFavorite !== undefined && (
            <Button
              iconOnly
              rounded='full'
              small
              className={clsx('ml-auto !border-transparent bg-gray-200', {
                '!text-gray focus:text-gray': !isMyFavorite,
                'text-error focus:text-error': isMyFavorite,
              })}
              onClick={handleFavorite}
              intent='secondary'
            >
              <HeartFillIcon size={18} />
            </Button>
          )}
        </View>
      </View>
      {action && (
        <View row className='items-end justify-between gap-3 mt-5'>
          <CampaignProgress
            raised={Number(campaign.raisedAmount)}
            goal={Number(campaign.goalAmount)}
          />
          {canDonate && (
            <Button
              small
              tag={Link}
              to={'/campaigns/' + campaign.id + '/donation/campaign'}
            >
              Donate
            </Button>
          )}
        </View>
      )}
    </View>
  );
};

export default CampaignCard;
