import { useState } from 'react';
import { useQuery } from 'react-query';
import { Text, View } from '../../lib';
import { getPastDonations } from '../../service';
import CampaignCard from '../campaign/CampaignCard';
import OrganizationCard from '../organization/OrganizationCard';
import LoaderSkeleton from '../common/LoaderSkeleton';
import Pagination from '../common/Pagination';

import image from '../../assets/img/illustrations/error.png';

const PastDonations = () => {
  const [page, setPage] = useState(1);
  const [pastDonations, setPastDonations] = useState<PastDonations>();
  const { isLoading, refetch } = useQuery(
    ['past-donations', page],
    () => getPastDonations({ page }),
    {
      onSuccess(data) {
        setPastDonations((prevData) =>
          prevData
            ? {
                items: [...prevData.items, ...data.items],
                meta: data.meta,
              }
            : data
        );
      },
    }
  );

  const loadMore = () => {
    if (pastDonations?.meta) {
      setPage(pastDonations.meta.currentPage + 1);
    }
  };

  return (
    <div className='space-y-5'>
      {isLoading && !pastDonations ? (
        <LoaderSkeleton />
      ) : pastDonations?.items.length ? (
        <>
          <div className='grid gap-4 lg:grid-cols-2 2xl:grid-cols-3'>
            {pastDonations.items.map(
              ({
                id,
                donationToType,
                campaign,
                organization,
                amount,
                createdAt,
              }) => {
                switch (donationToType) {
                  case 'campaign':
                    return (
                      <CampaignCard
                        createdAt={createdAt}
                        amount={amount / 100}
                        campaign={campaign!}
                        refetch={refetch}
                        disabled
                        key={id}
                      />
                    );
                  case 'organization':
                    return (
                      <OrganizationCard
                        createdAt={createdAt}
                        amount={amount / 100}
                        organization={organization!}
                        refetch={refetch}
                        disabled
                        showDonation
                        key={id}
                      />
                    );
                  default:
                    return null;
                }
              }
            )}
          </div>
          {pastDonations?.meta && (
            <Pagination
              loading={isLoading}
              meta={pastDonations.meta}
              loadMore={loadMore}
            />
          )}
        </>
      ) : (
        <View container className='mt-5'>
          <View className='flex-1 text-center justify-evenly'>
            <img src={image} alt='Icon' className='mx-auto w-52' />
            <div className='mt-5'>
              <Text type='h2' className='mb-3 text-2xl'>
                No donations!
              </Text>
              <Text>You don't have any donations yet.</Text>
            </div>
          </View>
        </View>
      )}
    </div>
  );
};

export default PastDonations;
