import { ReactElement, RefObject } from 'react';
import { Input } from '.';
import { useAutocomplete } from '../hooks/useAutocomplete';
import { InputProps } from './Input';

interface AutoCompleteAddressProps extends InputProps {
  onPlaceSelected: (address: Address) => void;
  children?: (ref: RefObject<HTMLInputElement>) => ReactElement;
}

const AutoCompleteAddress = ({
  onPlaceSelected,
  children,
  ...rest
}: AutoCompleteAddressProps) => {
  const { ref } = useAutocomplete(
    {
      componentRestrictions: { country: ['ca', 'us'] },
      fields: ['address_components', 'geometry'],
      types: ['address'],
    },
    onPlaceSelected
  );

  return children ? children(ref) : <Input ref={ref} {...rest} />;
};

export default AutoCompleteAddress;
