import { FC } from 'react';
import { toast } from 'react-toastify';
import copy from 'copy-to-clipboard';
import { Button, View } from '../../lib';
import ArrowLeftLineIcon from 'remixicon-react/ArrowLeftLineIcon';
import ExternalLinkLineIcon from 'remixicon-react/ExternalLinkLineIcon';
import clsx from 'clsx';
import useGo from '../../hooks/useGo';
import Heart3LineIcon from 'remixicon-react/Heart3LineIcon';
import Heart3FillIcon from 'remixicon-react/Heart3FillIcon';
import { errorHandler, isWeb, successHandler } from '../../utils';

interface BannerProps {
  src: string;
  params: ShareParams;
  link?: string;
  favorite?: boolean;
  onFavorite?: VoidFunction;
  className?: string;
}

const Banner: FC<BannerProps> = ({
  src,
  params,
  favorite = false,
  onFavorite,
  className = '',
}) => {
  const goBack = useGo(-1);

  const share = () => {
    const shareableLink = isWeb
      ? window.location.href
      : 'https://www.giveummah.com/app?type=' +
        params.type +
        '&id=' +
        params.id;

    if (window?.plugins?.socialsharing) {
      window.plugins.socialsharing.share(
        params.title,
        undefined,
        src,
        shareableLink,
        () => {
          successHandler({
            statusCode: 200,
            message: 'Successfully shared',
            data: null,
          });
        },
        () => {
          errorHandler({ statusCode: 500, message: 'Something went wrong' });
        }
      );
    } else {
      copy(shareableLink, { debug: true, message: 'Press #{key} to copy' });
      toast('URL is copied in the clipboard', {
        type: 'success',
      });
    }
  };

  return (
    <View
      className={clsx('h-32 p-4 rounded-2xl relative', className)}
      scroll={false}
      row
    >
      <View
        className={clsx(
          'absolute left-0 top-0 right-0 bottom-0  bg-center bg-no-repeat bg-cover filter brightness-95',
          className
        )}
        style={{ backgroundImage: `url(${src})` }}
      />
      <Button
        className='relative'
        small
        iconOnly
        rounded='full'
        onClick={goBack}
      >
        <ArrowLeftLineIcon size={18} />
      </Button>
      {onFavorite && (
        <Button
          small
          iconOnly
          rounded='full'
          className='relative ml-auto'
          onClick={onFavorite}
        >
          {favorite ? (
            <Heart3FillIcon size={18} />
          ) : (
            <Heart3LineIcon size={18} />
          )}
        </Button>
      )}
      <Button
        small
        iconOnly
        rounded='full'
        className={clsx('relative', {
          'ml-3': onFavorite,
          'ml-auto': !onFavorite,
        })}
        onClick={share}
      >
        <ExternalLinkLineIcon size={18} />
      </Button>
    </View>
  );
};

export default Banner;
