import { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import clsx from 'clsx';
import useAuth from '../hooks/useAuth';
import { Button, Input, Text } from '../lib';
import AuthContainer from '../components/auth/AuthContainer';
import useValidation from '../hooks/useValidation';
import Joi from 'joi';
import Logo from '../components/common/Logo';

// import logo from '../assets/img/logo.png';

const schema = Joi.object({
  email: Joi.string()
    .email({ tlds: { allow: false } })
    .required()
    .messages({
      'string.empty': 'Email is required',
      'string.email': 'Email must be a valid email address',
    }),
  password: Joi.string().min(8).required().messages({
    'string.empty': 'Password is required',
    'string.min': 'Password must be at least 8 characters',
  }),
});

const Signin = () => {
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const { signin, user } = useAuth();

  // TODO: Need to redirect to 'from'
  const from = '/';

  const { register, onSubmit, errors } = useValidation(
    schema,
    (data) => {
      setLoading(true);
      signin(data, () => {
        setLoading(false);
      });
    },
    { mode: 'onBlur' }
  );

  useEffect(() => {
    if (user) {
      navigate(from, { replace: true });
    }
  }, [user, from, navigate]);

  return (
    <AuthContainer
      caption='Welcome'
      title={
        <>
          Sign In to your
          <br />
          account.
        </>
      }
    >
      <div className='mx-auto mt-8 mb-16'>
        <Logo width={230} />
      </div>
      <form onSubmit={onSubmit}>
        <Input
          placeholder='Enter your email'
          type='email'
          error={errors?.email?.message}
          {...register('email')}
        />
        <Input
          className='mt-6'
          type='password'
          placeholder='Enter your password'
          error={errors?.password?.message}
          {...register('password')}
        />
        <Text
          type='small'
          className='block mt-3 font-medium text-right text-primary'
        >
          <Link to='/auth/forgot-password'>Forgot Password?</Link>
        </Text>

        <Button
          type='submit'
          block
          disabled={loading}
          loading={loading}
          className='mt-14'
        >
          Sign in
        </Button>
      </form>
      <Button
        tag={Link}
        to='/auth/signup'
        intent='secondary'
        block
        className={clsx('mt-4', { disabled: true })}
        disabled={loading}
      >
        Sign up
      </Button>
    </AuthContainer>
  );
};

export default Signin;
