import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button, View } from '../../lib';
import SavedCard from '../common/cards/SavedCard';
import { confirmDonationWithSavedCard, getProfile } from '../../service';
import { errorHandler } from '../../utils';
import useAuth from '../../hooks/useAuth';

type DonationConfirmationPropsWithStripeIntent = {
  stripeIntent: any;
  loading?: never;
  onSubmit?: never;
  amount?: never;
};

type DonationConfirmationPropsWithRecurring = {
  loading: boolean;
  onSubmit: VoidFunction;
  amount: number;
  stripeIntent?: never;
};

type DonationConfirmationProps =
  | DonationConfirmationPropsWithStripeIntent
  | DonationConfirmationPropsWithRecurring;

const DonationConfirmation = ({
  stripeIntent,
  loading: submitting,
  amount,
  onSubmit,
}: DonationConfirmationProps) => {
  const { setUser } = useAuth();
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  const handleDonationConfirmation = () => {
    if (stripeIntent) {
      setLoading(true);
      confirmDonationWithSavedCard({ payment_intent: stripeIntent.id })
        .then(() => {
          getProfile().then(setUser).catch(errorHandler);
          navigate('/success', { replace: true });
        })
        .finally(() => setLoading(false));
    } else {
      onSubmit && onSubmit();
    }
  };

  return (
    <View className='flex-1'>
      <SavedCard />
      <p className='mt-2'>
        You are about to
        {stripeIntent ? ' donate ' : ' start a recurring donation of '}
        <span className='font-bold text-black'>
          ${stripeIntent ? stripeIntent.amount / 100 : amount}
        </span>{' '}
        from your saved card, please confirm.
      </p>
      <div className='flex-1' />
      <Button
        onClick={handleDonationConfirmation}
        loading={loading || submitting}
        disabled={loading || submitting}
      >
        Confirm
      </Button>
    </View>
  );
};

export default DonationConfirmation;
