import { useState } from 'react';
import { useQuery } from 'react-query';
import { Container, Text, View } from '../lib';
import Campaigns from '../components/campaign/Campaigns';
import { getCampaigns, getOrganizations } from '../service';
import Tab, { TabItem } from '../components/common/Tab';
import Banner from '../components/common/Banner';
import Organizations from '../components/organization/Organizations';
import usePermission from '../hooks/usePermission';
import { uniqMerge } from '../utils';

const tabs: TabItem[] = [
  { value: 'organizations', content: 'Organizations' },
  { value: 'campaigns', content: 'Campaigns' },
];

const Favorites = () => {
  const [type, setType] = useState(tabs[0]);
  const [campaigns, setCampaigns] = useState<Campaigns>();
  const [organizations, setOrganizations] = useState<Organizations>();
  const [pageCampaign, setPageCampaign] = useState(1);
  const [pageOrganization, setPageOrganization] = useState(1);
  const { canMakeCampaignFavorite } = usePermission();

  const { isLoading: isLoadingCampaigns, refetch: refetchCampaigns } = useQuery(
    ['campaigns', 'favorite', pageCampaign],
    () =>
      getCampaigns({
        isMyFavorite: true,
        page: pageCampaign,
      }),
    {
      enabled: canMakeCampaignFavorite,
      onSuccess(data) {
        setCampaigns((prevData) => uniqMerge(data, prevData));
      },
    }
  );

  const { isLoading: isLoadingOrganizations, refetch: refetchOrganizations } =
    useQuery(
      ['organizations', 'favorite', pageOrganization],
      () =>
        getOrganizations({
          isMyFavorite: true,
          page: pageOrganization,
        }),
      {
        onSuccess({ data }) {
          setOrganizations((prevData) => uniqMerge(data, prevData));
        },
      }
    );

  return (
    <View content>
      <Banner sticky>
        <Container className='py-5 space-y-5'>
          <Text type='h2' className='sticky top-0 z-10 text-2xl'>
            Favorites
          </Text>
          {canMakeCampaignFavorite && (
            <Tab tabs={tabs} currentTab={type} onTabChange={setType} />
          )}
        </Container>
      </Banner>
      <View container className='flex-1'>
        <div className='py-5 space-y-5'>
          {type.value === 'campaigns' && (
            <Campaigns
              loading={isLoadingCampaigns}
              campaigns={campaigns}
              refetch={refetchCampaigns}
              setPage={setPageCampaign}
            />
          )}
          {type.value === 'organizations' && (
            <Organizations
              loading={isLoadingOrganizations}
              organizations={organizations}
              refetch={refetchOrganizations}
              setPage={setPageOrganization}
            />
          )}
        </div>
      </View>
    </View>
  );
};

export default Favorites;
