import { Dispatch, SetStateAction, useState } from 'react';
import CloseCircleFillIcon from 'remixicon-react/CloseCircleFillIcon';
import { Avatar, Button, Input, Text, View } from '../../lib';
import useAuth from '../../hooks/useAuth';
import { errorHandler, getAssetUrl, successHandler } from '../../utils';
import useValidation from '../../hooks/useValidation';
import ApiService, { getProfile } from '../../service';
import Joi from 'joi';
import useRole from '../../hooks/useRole';
import CameraLineIcon from 'remixicon-react/CameraLineIcon';
import UploadImage from '../common/UploadImage';

interface EditProfileProps {
  onClose: () => void;
}

const EditProfile = ({ onClose }: EditProfileProps) => {
  const [loading, setLoading] = useState(false);
  const { user, setUser } = useAuth();
  const isOrganization = useRole(['organization']);

  const schemaObject = isOrganization
    ? {
        name: Joi.string().required().messages({
          'string.empty': 'Name is required',
        }),
      }
    : {
        firstName: Joi.string().required().messages({
          'string.empty': 'First name is required',
        }),
        lastName: Joi.string().required().messages({
          'string.empty': 'Last name is required',
        }),
      };

  const { register, onSubmit, onReset, errors } = useValidation(
    Joi.object(schemaObject),
    (data) => {
      setLoading(true);
      ApiService.updateProfile(data)
        .then((user) => {
          setUser(user);
          onClose();
        })
        .catch(errorHandler)
        .finally(() => {
          setLoading(false);
        });
    },
    {
      mode: 'onBlur',
      defaultValues: isOrganization
        ? {
            name: user.name,
          }
        : {
            firstName: user.firstName,
            lastName: user.lastName,
          },
    }
  );

  const handleProfilePictureChange = (
    file: File,
    setFileUploading: Dispatch<SetStateAction<boolean>>
  ) => {
    const formData = new FormData();
    formData.append('file', file, file.name);

    ApiService.updateProfileImage(formData)
      .then(() => {
        getProfile().then((user) => {
          setUser(user);
          successHandler({
            statusCode: 0,
            message: 'Profile image updated successfully!',
            data: null,
          });
          onClose();
        });
      })
      .catch(errorHandler)
      .finally(() => {
        setFileUploading(false);
      });
  };

  return (
    <>
      <View row className='items-center justify-between'>
        <Text type='h1'>Edit profile</Text>
        <CloseCircleFillIcon size={24} onClick={() => !loading && onClose()} />
      </View>
      <View className='items-center mt-6 mb-11'>
        <div className='relative'>
          <Avatar
            src={getAssetUrl(user.profileImage)}
            name={user.name}
            className='object-cover'
            size='large'
            thumbnail
          />
          <UploadImage
            handleFileUpload={handleProfilePictureChange}
            options={{
              targetWidth: 500,
              targetHeight: 500,
            }}
          >
            {(fileUploading, handleClick, input) => (
              <Button
                iconOnly
                rounded='full'
                className='absolute transform -translate-x-1/2 -translate-y-1/2 left-1/2 top-full'
                disabled={fileUploading}
                loading={fileUploading}
                small
                onClick={handleClick}
              >
                {input}
                <CameraLineIcon size={18} />
              </Button>
            )}
          </UploadImage>
        </div>
      </View>
      <form onSubmit={onSubmit} onReset={onReset} className='space-y-6'>
        {isOrganization ? (
          <Input
            label='Name'
            error={errors?.name?.message}
            {...register('name')}
          />
        ) : (
          <>
            <Input
              label='First name'
              error={errors?.firstName?.message}
              {...register('firstName')}
            />
            <Input
              label='Last name'
              error={errors?.lastName?.message}
              {...register('lastName')}
            />
          </>
        )}

        <Button
          block
          className='mt-56'
          type='submit'
          disabled={loading}
          loading={loading}
        >
          Save
        </Button>
      </form>
    </>
  );
};

export default EditProfile;
