import { ChangeEvent, useState } from 'react';
import { Button, Input, Switch, Text, TextArea, View } from '../../lib';
import BackButton from '../common/BackButton';
import Progress from '../common/Progress';
import CloseFillIcon from 'remixicon-react/CloseFillIcon';
import ImageAddLineIcon from 'remixicon-react/ImageAddLineIcon';
import useGo from '../../hooks/useGo';
import {
  fileToBase64,
  currencyFormat,
  errorHandler,
  successHandler,
} from '../../utils';
import { createCampaign, updateCampaign } from '../../service';
import TouchInput from '../common/TouchInput';
import UploadImage from '../common/UploadImage';

interface CampaignCreateProps {
  initialData?: {
    id: ID;
    title: string;
    description: string;
    goalAmount: string;
    thumbnailImage: string;
    file?: null;
  };
  onClose?: () => void;
}

const initialCampaign = {
  title: '',
  description: '',
  goalAmount: '',
  file: null as unknown as File,
};

const CampaignCreate = ({ initialData, onClose }: CampaignCreateProps) => {
  const [loading, setLoading] = useState(false);
  const [step, setStep] = useState(1);
  const [campaign, setCampaign] = useState(initialData || initialCampaign);
  const [previewUrl, setPreviewUrl] = useState('');
  const goBack = useGo(-1);

  const handleChange = (e: ChangeEvent<any>) => {
    // HTMLInputElement | HTMLTextAreaElement
    setCampaign((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const handleFileChange = (file: File) => {
    setCampaign((prev) => ({ ...prev, file }));
    fileToBase64(file)
      .then((url) => {
        setPreviewUrl(url);
        setLoading(false);
        setStep((prevStep) => prevStep + 1);
      })
      .catch(errorHandler);
  };

  const handleSubmit = () => {
    if (Object.values(campaign).filter((item) => !!item).length < 4) {
      errorHandler({
        statusCode: 0,
        message: 'Fill up all the required fields to create the campaign',
      });
      return;
    }

    setLoading(true);
    const campaignFormData = new FormData();
    campaignFormData.append('title', campaign.title);
    campaignFormData.append('description', campaign.description);
    campaignFormData.append('goalAmount', campaign.goalAmount);
    if (campaign.file) {
      campaignFormData.append('file', campaign.file, campaign.file.name);
    }

    if (initialData) {
      updateCampaign(initialData.id, campaignFormData)
        .then((response) => {
          onClose && onClose();
          successHandler(response);
        })
        .catch(errorHandler)
        .finally(() => {
          setLoading(false);
        });
    } else {
      createCampaign(campaignFormData)
        .then((response) => {
          onClose && onClose();
          successHandler(response);
        })
        .catch(errorHandler)
        .finally(() => {
          setLoading(false);
        });
    }
  };

  return (
    <View container className='py-5 lg:min-w-[448px] lg:px-5' content>
      <div>
        {step === 1 ? (
          <Button
            iconOnly
            rounded='full'
            className='bg-gray border-gray'
            onClick={onClose || goBack}
          >
            <CloseFillIcon size={24} />
          </Button>
        ) : (
          <BackButton onClick={() => setStep((prevState) => prevState - 1)} />
        )}
      </div>
      <Text type='small' className='py-3 text-center'>
        Step {step} of 4
      </Text>
      <View row className='space-x-2'>
        {[1, 2, 3, 4].map((s) => (
          <Progress color='primary' progress={step >= s ? 1 : 0} key={s} />
        ))}
      </View>
      <View className='flex-1 my-8'>
        {step === 1 && (
          <View>
            <Text type='h2' className='!text-primary text-center mb-8'>
              Enter your goal here
            </Text>
            <View
              className='items-center justify-center p-5 text-5xl font-medium border-4 rounded-lg bg-primary-100 border-primary text-primary-600'
              row
            >
              <TouchInput
                type='number'
                name='goalAmount'
                value={campaign.goalAmount}
                onChange={handleChange}
                formatter={(value) => currencyFormat(Number(value))}
                placeholder='Amount'
              />
            </View>
          </View>
        )}
        {step === 2 && (
          <View>
            <Text type='h2' className='!text-primary text-center mb-8'>
              Upload campaign photo
            </Text>
            <View className='items-center justify-center h-48 border border-dashed rounded-lg bg-primary-100 border-primary'>
              <UploadImage handleFileUpload={handleFileChange}>
                {(fileUploading, handleClick, input) => (
                  <Button
                    disabled={fileUploading}
                    loading={fileUploading}
                    onClick={handleClick}
                  >
                    {input}
                    <ImageAddLineIcon size={22} className='inline-block mr-3' />
                    Upload a photo
                  </Button>
                )}
              </UploadImage>
            </View>
          </View>
        )}
        {step === 3 && (
          <View>
            <Text type='h2' className='!text-primary text-center mb-8'>
              Confirm your photo
            </Text>
            <View className='overflow-hidden rounded-lg'>
              {previewUrl && <img src={previewUrl} alt='Campaign Banner' />}
              {!previewUrl && initialData && (
                <img src={initialData.thumbnailImage} alt='Campaign Banner' />
              )}
            </View>
          </View>
        )}
        {step === 4 && (
          <View>
            <Text type='h2' className='!text-primary text-center mb-8'>
              Campaign details
            </Text>
            <View className='space-y-5'>
              <Input
                label='Campaign title'
                name='title'
                value={campaign.title}
                onChange={handleChange}
              />
              <TextArea
                label='Campaign description'
                name='description'
                value={campaign.description}
                onChange={handleChange}
                rows={4}
              />
              <View
                className='items-center space-x-3 cursor-pointer focus:bg-transparent'
                row
              >
                <Switch defaultChecked /> <span>Active campaign</span>
              </View>
            </View>
          </View>
        )}
      </View>
      <View className='space-y-5'>
        {step === 3 && (
          <Button block intent='secondary' onClick={() => setStep(2)}>
            Change
          </Button>
        )}
        {step < 4 ? (
          <Button
            block
            onClick={() => setStep((prevStep) => prevStep + 1)}
            loading={loading}
            disabled={loading}
          >
            Next
          </Button>
        ) : (
          <Button
            block
            onClick={handleSubmit}
            loading={loading}
            disabled={loading}
          >
            Submit
          </Button>
        )}
      </View>
    </View>
  );
};

export default CampaignCreate;
