import { useEffect } from 'react';
import AuthProvider from './context/AuthProvider';
import QueryProvider from './context/QueryProvider';
import AppRoutes from './routes';
import { ToastContainer } from 'react-toastify';
import { iPhoneVersion, isIphone } from './utils';

import 'react-toastify/dist/ReactToastify.css';

const App = () => {
  useEffect(() => {
    if (isIphone()) {
      document.documentElement.classList.add(iPhoneVersion());
    }
  }, []);

  return (
    <AuthProvider>
      <QueryProvider>
        <AppRoutes />
        <ToastContainer
          position='top-center'
          autoClose={10000}
          closeOnClick
          pauseOnHover
          newestOnTop
        />
      </QueryProvider>
    </AuthProvider>
  );
};

export default App;
