import { Button } from '../../lib';

interface PaginationProps {
  loading: boolean;
  meta: Meta;
  loadMore: VoidFunction;
}

const Pagination = ({ loading, meta, loadMore }: PaginationProps) => {
  if (!meta.totalPages || meta.totalPages === meta.currentPage) {
    return null;
  }

  return (
    <Button
      onClick={loadMore}
      intent='secondary'
      loading={loading}
      disabled={loading}
      block
    >
      Load more
    </Button>
  );
};

export default Pagination;
