import { ChangeEvent, useState } from 'react';
import useAuth from '../hooks/useAuth';
import { Button, Switch, Text, View } from '../lib';
import Dialog from '../components/common/Dialog';
import ChangePassword from '../components/auth/ChangePassword';
import EditProfile from '../components/profile/EditProfile';
import ArrowRightSLineIcon from 'remixicon-react/ArrowRightSLineIcon';
import { useNavigate } from 'react-router-dom';
import usePermission from '../hooks/usePermission';
import { errorHandler, openCustomURL, successHandler } from '../utils';
import { Link } from 'react-router-dom';
import ApiService, { getStripeAccount } from '../service';
import { useQuery } from 'react-query';
import AnchorLink from '../components/common/AnchorLink';
import BankInformationDialog from '../components/settings/BankInformationDialog';

const Settings = () => {
  const [loading, setLoading] = useState(false);
  const [bankInfLoading, setBankInfoLoading] = useState(false);
  const { user, setUser, signout } = useAuth();
  const [pushNotificationEnabled, setPushNotificationEnabled] = useState(
    user.pushNotificationEnabled
  );
  const navigate = useNavigate();
  const { canAddPaymentMethod, canAddBankInformation } = usePermission();
  const {
    isLoading: bankInfFetching,
    data: bankInfo,
    refetch: refetchBankInfo,
  } = useQuery('bank-info', getStripeAccount, {
    enabled: canAddBankInformation,
  });

  const handleAddBankInformation = () => {
    setBankInfoLoading(true);
    // const returnUrl = `${window.location.protocol}//${window.location.pathname}#/success`;
    // const refreshUrl = `${window.location.protocol}//${window.location.pathname}#/settings`;
    // const returnUrl = `http://localhost:3000/#/success`;
    // const refreshUrl = `http://localhost:3000/#/settings`;

    const returnUrl = `https://app.giveummah.com`;
    const refreshUrl = `https://app.giveummah.com`;

    ApiService.createStripeAccount({ returnUrl, refreshUrl })
      .then((response) => {
        const inAppBrowserRef = openCustomURL(
          response.url,
          async () => {
            alert('Exited');
            await refetchBankInfo();
          },
          async (e: any) => {
            alert('Load Start');
            if (e.url.indexOf('https://app.giveummah.com') === 0) {
              await refetchBankInfo();
              inAppBrowserRef.close();
            }
          }
        );

        // alert(inAppBrowserRef);

        // if (inAppBrowserRef) {
        //   inAppBrowserRef.addEventListener('exit', async () => {
        //     alert('Exited');
        //     await refetchBankInfo();
        //   });
        //   inAppBrowserRef.addEventListener('loadstart', async (e: any) => {
        //     alert('Load Start');
        //     if (e.url.indexOf('https://app.giveummah.com') === 0) {
        //       await refetchBankInfo();
        //       inAppBrowserRef.close();
        //     }
        //   });
        // }
      })
      .catch(errorHandler)
      .finally(() => {
        setBankInfoLoading(false);
      });
  };

  const handlePushNotificationEnabled = ({
    target: { checked },
  }: ChangeEvent<HTMLInputElement>) => {
    setPushNotificationEnabled(checked);
    ApiService.updateProfile({ pushNotificationEnabled: checked })
      .then((user) => {
        setUser(user);
        successHandler({
          statusCode: 0,
          message: `Push notification ${checked ? 'enabled' : 'disabled'}`,
          data: null,
        });
      })
      .catch((error) => {
        setPushNotificationEnabled(!checked);
        errorHandler(error);
      });
  };

  const handleSignout = () => {
    setLoading(true);
    signout(() => {
      setLoading(false);
      navigate('/auth/signin', { replace: true });
    });
  };

  return (
    <View className='pt-12 pb-5 container-mini' content>
      <Text type='h1'>Settings</Text>
      <div className='flex-1 mt-12 mb-8'>
        <ul className='space-y-8'>
          <li>
            <Text type='h4' className='font-medium'>
              Account settings
            </Text>
          </li>
          <li>
            <Dialog
              activator={(setOpen) => (
                <View
                  className='items-center justify-between cursor-pointer focus:bg-transparent'
                  onClick={() => setOpen(true)}
                  row
                >
                  Edit profile <ArrowRightSLineIcon size={24} />
                </View>
              )}
            >
              {(setOpen) => (
                <View className='m-6 min-w-[280px] lg:min-w-[448px]'>
                  <EditProfile onClose={() => setOpen(false)} />
                </View>
              )}
            </Dialog>
          </li>
          <li>
            <Dialog
              activator={(setOpen) => (
                <View
                  className='items-center justify-between cursor-pointer focus:bg-transparent'
                  onClick={() => setOpen(true)}
                  row
                >
                  Change password <ArrowRightSLineIcon size={24} />
                </View>
              )}
            >
              {(setOpen) => (
                <View className='m-6 min-w-[280px] lg:min-w-[448px]'>
                  <ChangePassword
                    onClose={() => setOpen(false)}
                    onSignout={handleSignout}
                  />
                </View>
              )}
            </Dialog>
          </li>
          {canAddPaymentMethod && (
            <li>
              <Link
                className='flex items-center justify-between cursor-pointer focus:bg-transparent'
                to='/settings/payments'
              >
                Payment Methods <ArrowRightSLineIcon size={24} />
              </Link>
            </li>
          )}
          {canAddBankInformation && (
            <li>
              <Link
                className='flex items-center justify-between cursor-pointer focus:bg-transparent'
                to='/settings/amount-raised'
              >
                Amount Raised <ArrowRightSLineIcon size={24} />
              </Link>
            </li>
          )}
          <li>
            <View
              className='items-center justify-between cursor-pointer focus:bg-transparent'
              row
            >
              Push notifications{' '}
              <Switch
                checked={pushNotificationEnabled}
                onChange={handlePushNotificationEnabled}
              />
            </View>
          </li>
          <li>
            <Text type='h4' className='font-medium'>
              More
            </Text>
          </li>
          <li>
            <AnchorLink
              className='flex items-center justify-between cursor-pointer focus:bg-transparent'
              href='https://www.giveummah.com/about-us/'
            >
              About us <ArrowRightSLineIcon size={24} />
            </AnchorLink>
          </li>
          <li>
            <AnchorLink
              className='flex items-center justify-between cursor-pointer focus:bg-transparent'
              href='https://www.giveummah.com/privacy-policy-2/'
            >
              Privacy policy <ArrowRightSLineIcon size={24} />
            </AnchorLink>
          </li>
          <li>
            <AnchorLink
              className='flex items-center justify-between cursor-pointer focus:bg-transparent'
              href='https://www.giveummah.com/terms-conditions/'
            >
              Terms and conditions <ArrowRightSLineIcon size={24} />
            </AnchorLink>
          </li>
        </ul>
      </div>

      {canAddBankInformation &&
        (bankInfo?.details_submitted ? (
          <BankInformationDialog>
            <Button block className='my-5'>
              Edit bank information
            </Button>
          </BankInformationDialog>
        ) : (
          <Button
            className='my-5'
            onClick={handleAddBankInformation}
            loading={bankInfLoading || bankInfFetching}
            disabled={bankInfLoading || bankInfFetching}
          >
            Add bank information
          </Button>
        ))}

      <Button
        onClick={handleSignout}
        intent='secondary'
        disabled={loading}
        loading={loading}
      >
        Logout
      </Button>
    </View>
  );
};

export default Settings;
