import { FC, useState } from 'react';
import { useQuery } from 'react-query';
import { Button, Container, Text, View } from '../../lib';
import Banner from './Banner';
import Campaigns from '../campaign/Campaigns';
import { errorHandler, getAssetUrl, successHandler } from '../../utils';
import {
  favorite,
  getCampaigns,
  getOrganization,
  unfavorite,
} from '../../service';
import useRole from '../../hooks/useRole';
import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';
import Tab, { TabItem } from '../common/Tab';

import HandHeartFillIcon from 'remixicon-react/HandHeartFillIcon';
import BuildingLineIcon from 'remixicon-react/BuildingLineIcon';
import PhoneLineIcon from 'remixicon-react/PhoneLineIcon';

// import bannerFallBack from '../../assets/img/banner-feed-individual-3x.png';
interface OrganizationProps {
  user?: User;
}

const tabs: TabItem[] = [
  { value: 'active', content: 'Active Campaigns' },
  { value: 'past', content: 'Past Campaigns' },
];

const Organization: FC<OrganizationProps> = ({ user }) => {
  const [campaignStatus, setCampaignStatus] = useState(tabs[0]);
  const [profile, setProfile] = useState(user);
  const [page, setPage] = useState(1);
  const [campaigns, setCampaigns] = useState<Campaigns>();
  const params = useParams();

  const { refetch: refetchProfile } = useQuery(
    ['user', params.id],
    () => getOrganization(Number(params.id || profile?.id)),
    {
      retry: false,
      onSuccess(response: SuccessResponse<User>) {
        setProfile(response.data);
      },
    }
  );

  const { isLoading, refetch } = useQuery(
    ['campaigns', profile?.id, page],
    () =>
      getCampaigns({
        createdBy: profile?.id,
        withInactive: false,
        onlyUnfulfilled: true,
        page,
      }),
    {
      onSuccess(data) {
        setCampaigns((prevData) =>
          prevData
            ? {
                items: [...prevData.items, ...data.items],
                meta: data.meta,
              }
            : data
        );
      },
      enabled: !!profile?.id,
    }
  );

  const isOrganization = useRole(['organization']);

  const handleFavorite = () => {
    if (!profile) return;
    if (profile.isMyFavorite) {
      unfavorite(profile.id, 'organizations')
        .then((response) => {
          successHandler(response);
          refetchProfile();
        })
        .catch(errorHandler);
    } else {
      favorite(profile.id, 'organizations')
        .then((response) => {
          successHandler(response);
          refetchProfile();
        })
        .catch(errorHandler);
    }
  };

  return (
    <View content>
      <div className='pt-5 bg-white'>
        <Container>
          <Banner
            src={getAssetUrl(profile?.profileImage)}
            params={{
              title: profile?.name || 'Organization',
              type: 'organization',
              id: Number(params.id || profile?.id),
            }}
            favorite={!!profile?.isMyFavorite}
            onFavorite={handleFavorite}
          />
          <Text type='h2' className='mt-4'>
            {profile?.name}
          </Text>
          <Text type='small' className='block mt-4'>
            <PhoneLineIcon
              className='inline-block mr-2 text-primary'
              size={20}
            />
            <a href={`tel:${profile?.phone}`}>{profile?.phone}</a>
          </Text>
          <Text type='small' className='block mt-2'>
            <BuildingLineIcon
              className='inline-block mr-2 text-primary'
              size={20}
            />
            {profile?.address?.formatted_address}
          </Text>
        </Container>
      </div>
      <div className='sticky top-0 z-10 bg-white'>
        {isOrganization ? (
          <Tab
            tabs={tabs}
            currentTab={campaignStatus}
            onTabChange={setCampaignStatus}
            className='mb-3 mt-7'
            container
          />
        ) : (
          <View container>
            {profile?.isBankAddedSuccessfully && (
              <Button
                tag={Link}
                className='mt-7'
                to={'/organizations/' + params.id + '/donation/organization'}
              >
                <HandHeartFillIcon size={22} className='inline-block mr-3' />
                General Donation
              </Button>
            )}
            <Text type='h4' className='my-5 font-medium'>
              Active Campaigns
            </Text>
          </View>
        )}
      </div>
      <View container className='my-5 space-y-5'>
        <Campaigns
          loading={isLoading}
          campaigns={
            campaigns && {
              ...campaigns,
              items: campaigns.items.filter((isActive) =>
                campaignStatus.value === 'active' ? isActive : !isActive
              ),
            }
          }
          refetch={refetch}
          setPage={setPage}
          action={campaignStatus.value === 'active'}
        />
      </View>
    </View>
  );
};

export default Organization;
