import { useState, MouseEvent } from 'react';
import { useWatch } from 'react-hook-form';
import { Button, Input, Text, View } from '../../lib';
import useValidation from '../../hooks/useValidation';
import ApiService from '../../service';
import usePasswordCriteria from '../../hooks/usePasswordCriteria';
import CheckboxCircleFillIcon from 'remixicon-react/CheckboxCircleFillIcon';
import CloseCircleFillIcon from 'remixicon-react/CloseCircleFillIcon';
import Joi from 'joi';
import { errorHandler, successHandler } from '../../utils';
interface ChangePasswordProps {
  onClose: (e: MouseEvent) => void;
  onSignout: () => void;
}

const schema = Joi.object({
  oldPassword: Joi.string().min(8).required().messages({
    'string.empty': 'Old password is required',
    'string.min': 'Old Password must be 8 characters length',
  }),
  newPassword: Joi.string()
    .pattern(
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*[|\\/~^:,;?!&%$@*+"#'()-.<=>[\]_`{}\d])[a-zA-Z|\\/~^:,;?!&%$@*+"#'()-.<=>[\]_`{}\d]{8,}$/
    )
    .required()
    .messages({
      'string.empty': 'New password is required',
      'string.pattern.base': 'New password must be fullfil following criteria',
    }),
  confirmNewPassword: Joi.any()
    .valid(Joi.ref('newPassword'))
    .required()
    .messages({
      'any.only': 'Confirm new password must match with new password',
    }),
});

export const PasswordValidationIcon = ({ isValid }: { isValid: boolean }) => (
  <CheckboxCircleFillIcon
    className={isValid ? 'text-primary' : 'text-gray-300'}
    size={12}
  />
);

const ChangePassword = ({ onClose, onSignout }: ChangePasswordProps) => {
  const [loading, setLoading] = useState(false);
  const { control, register, onSubmit, onReset, errors } = useValidation(
    schema,
    async (data) => {
      setLoading(true);
      ApiService.changePassword(data)
        .then((response) => {
          successHandler(response);
          onSignout();
        })
        .catch((error) => {
          errorHandler(error.data);
        })
        .finally(() => {
          onReset();
          setLoading(false);
        });
    },
    { mode: 'onBlur' }
  );

  const {
    hasRequiredLength,
    hasUppercaseCharacter,
    hasLowercaseCharacter,
    hasNumericCharacter,
    hasSpecialCharacter,
  } = usePasswordCriteria(
    useWatch({
      control,
      name: 'newPassword',
      defaultValue: '',
    })
  );

  return (
    <>
      <View row className='items-center justify-between'>
        <Text type='h1'>Change password</Text>
        <CloseCircleFillIcon
          size={24}
          onClick={(e) => !loading && onClose(e)}
        />
      </View>
      <form onSubmit={onSubmit} className='mt-10 mb-5 space-y-5'>
        <Input
          type='password'
          placeholder='Old password'
          error={errors?.oldPassword?.message}
          {...register('oldPassword')}
        />
        <Input
          type='password'
          placeholder='New password'
          error={errors?.newPassword?.message}
          {...register('newPassword')}
        />
        <Input
          type='password'
          placeholder='Re-enter password'
          error={errors?.confirmNewPassword?.message}
          {...register('confirmNewPassword')}
        />
        <Button type='submit' disabled={loading} loading={loading} block>
          Reset password
        </Button>
      </form>

      <Text type='h5'>Password must have the following:</Text>
      <ul className='mt-3 text-xs'>
        <li className='flex items-center space-x-1'>
          <PasswordValidationIcon isValid={hasRequiredLength} />
          <span>At least 8 characters</span>
        </li>
        <li className='flex items-center space-x-1'>
          <PasswordValidationIcon isValid={hasUppercaseCharacter} />
          <span>1 upper case</span>
        </li>
        <li className='flex items-center space-x-1'>
          <PasswordValidationIcon isValid={hasLowercaseCharacter} />
          <span>1 lower case</span>
        </li>
        <li className='flex items-center space-x-1'>
          <PasswordValidationIcon
            isValid={hasNumericCharacter || hasSpecialCharacter}
          />
          <span>1 number or special character</span>
        </li>
      </ul>
    </>
  );
};

export default ChangePassword;
