import {
  Dispatch,
  HTMLAttributes,
  ReactNode,
  SetStateAction,
  useRef,
  useState,
} from 'react';
import { Button, View } from '../../lib';
import { errorHandler, isWeb } from '../../utils';
import Dialog from './Dialog';

declare let window: any;

type CameraOptions = {
  targetWidth?: number;
  targetHeight?: number;
};

interface InputProps extends HTMLAttributes<HTMLInputElement> {
  name: string;
}
interface UploadImageProps {
  handleFileUpload: (
    file: File,
    setFileUploading: Dispatch<SetStateAction<boolean>>
  ) => void;
  options?: CameraOptions;
  inputProps?: InputProps;
  children: (
    isSelecting: boolean,
    handleClick: VoidFunction,
    input: ReactNode
  ) => ReactNode;
}

const UploadImage = ({
  handleFileUpload,
  options,
  inputProps,
  children,
}: UploadImageProps) => {
  const [photoIdDialog, setPhotoIdDialog] = useState(false);
  const [fileUploading, setFileUploading] = useState(false);
  const fileRef = useRef<HTMLInputElement>(null);

  const handleFileUploadWeb = () => {
    setFileUploading(true);
    const file = fileRef.current?.files![0] as File;
    handleFileUpload(file, setFileUploading);
  };

  const uploadId = (uploadType: 'Capture' | 'ChooseFromLibrary') => {
    setPhotoIdDialog(false);
    setFileUploading(true);
    if (isWeb) return;
    document.addEventListener('deviceready', () => {
      /**initials */
      if (uploadType === 'Capture') {
        capture();
      } else {
        chooseFromLibrary();
      }
    });
  };

  const capture = () => {
    window.navigator.camera.getPicture(onPhotoDataSuccess, onFail, {
      quality: 75,
      allowEdit: window.device.platform !== 'Android',
      correctOrientation: true,
      destinationType: window.Camera.DestinationType.DATA_URL,
      ...options,
    });
  };
  const chooseFromLibrary = () => {
    window.navigator.camera.getPicture(onPhotoDataSuccess, onFail, {
      quality: 75,
      allowEdit: window.device.platform !== 'Android',
      correctOrientation: true,
      destinationType: window.Camera.DestinationType.DATA_URL,
      sourceType: window.Camera.PictureSourceType.PHOTOLIBRARY,
      mediaType: window.Camera.MediaType.PICTURE,
      ...options,
    });
  };

  const dataUrlToFile = async (
    dataUrl: string,
    fileName: string
  ): Promise<File> => {
    const res: Response = await fetch(dataUrl);
    const blob: Blob = await res.blob();
    return new File([blob], fileName, { type: 'image/jpeg' });
  };

  const onPhotoDataSuccess = async (imageData: string) => {
    const file = await dataUrlToFile(
      'data:image/jpeg;base64,' + imageData,
      'avatar.jpeg'
    );
    handleFileUpload(file, setFileUploading);
  };

  const onFail = (error: any) => {
    alert(JSON.stringify(error, null, 2));
    cleanup();
    errorHandler({
      statusCode: 0,
      message: 'Upload failed',
    });
    setFileUploading(false);
  };

  const cleanup = () => {
    window.navigator.camera.cleanup();
  };

  return (
    <>
      {children(
        fileUploading,
        () => {
          if (isWeb) {
            fileRef.current?.click();
            return;
          }
          setPhotoIdDialog(true);
        },
        <input
          type='file'
          onChange={handleFileUploadWeb}
          ref={fileRef}
          hidden
          {...inputProps}
        />
      )}
      <Dialog isOpen={photoIdDialog}>
        {() => (
          <View row className='items-center justify-center my-6 space-x-4'>
            <Button
              small={false}
              iconOnly={false}
              rounded='full'
              onClick={() => uploadId('Capture')}
            >
              Capture
            </Button>
            <Button
              small={false}
              iconOnly={false}
              rounded='full'
              onClick={() => uploadId('ChooseFromLibrary')}
            >
              Library
            </Button>
          </View>
        )}
      </Dialog>
    </>
  );
};

export default UploadImage;
