import { HashRouter, BrowserRouter, Routes, Route } from 'react-router-dom';
import DonationProvider from '../context/DonationProvider';
import AppLayout, { AuthLayout } from '../layout';
import Signin from '../pages/Signin';
import Signup from '../pages/Signup';
import ForgotPassword from '../pages/ForgotPassword';
import ForgotPasswordVerify from '../components/auth/ForgotPasswordVerify';
import RequireAuth from './RequireAuth';
import Dashboard from '../pages/Dashboard';
import NotMatch from '../pages/NotMatch';
import Typography from '../pages/Typography';
import Profile from '../pages/Profile';
import CampaignCreate from '../components/campaign/CampaignCreate';
import CampaignDetail from '../components/campaign/CampaignDetail';
import Settings from '../pages/Settings';
import ComingSoon from '../pages/ComingSoon';
import RequirePermission from './RequirePermission';
import Donation from '../components/donation/Donation';
import SuccessScreen from '../components/common/screens/SuccessScreen';
import ErrorScreen from '../components/common/screens/ErrorScreen';
import Favorites from '../pages/Favorites';
import Organization from '../components/profile/Organization';
import Search from '../pages/Search';
import AmountRaised from '../pages/AmountRaised';
import Payments from '../pages/settings/Payments';
import { isWeb } from '../utils';

const AppRoutes = () => {
  const Router = isWeb ? BrowserRouter : HashRouter;

  return (
    <Router>
      <Routes>
        <Route
          path='/'
          element={
            <RequireAuth>
              <AppLayout />
            </RequireAuth>
          }
        >
          <Route path='' element={<Dashboard />} />
          <Route path='profile' element={<Profile />} />
          <Route path='settings' element={<Settings />} />
          <Route path='settings/payments' element={<Payments />} />
          <Route path='settings/amount-raised' element={<AmountRaised />} />
          <Route path='about' element={<ComingSoon />} />
          <Route path='search' element={<Search />} />
          <Route path='favorites' element={<Favorites />} />
          <Route path='campaigns'>
            <Route path='' element={<NotMatch />} />
            <Route
              path='create'
              element={
                <RequirePermission roles={['organization']}>
                  <CampaignCreate />
                </RequirePermission>
              }
            />
            <Route path=':id'>
              <Route path='' element={<CampaignDetail />} />
              <Route
                path='donation/:donationType'
                element={
                  <RequirePermission roles={['individual']}>
                    <DonationProvider>
                      <Donation />
                    </DonationProvider>
                  </RequirePermission>
                }
              />
            </Route>
          </Route>
          <Route path='organizations'>
            <Route path='' element={<NotMatch />} />
            <Route path=':id'>
              <Route path='' element={<Organization />} />
              <Route
                path='donation/:donationType'
                element={
                  <RequirePermission roles={['individual']}>
                    <DonationProvider>
                      <Donation />
                    </DonationProvider>
                  </RequirePermission>
                }
              />
            </Route>
          </Route>
          <Route path='success' element={<SuccessScreen />} />
          <Route path='error' element={<ErrorScreen />} />
        </Route>
        <Route path='auth' element={<AuthLayout />}>
          <Route path='signin' element={<Signin />} />
          <Route path='signup' element={<Signup />} />
          <Route path='forgot-password'>
            <Route path='' element={<ForgotPassword />} />
            <Route path='verify' element={<ForgotPasswordVerify />} />
          </Route>
        </Route>
        <Route path='typography' element={<Typography />} />
        <Route path='*' element={<NotMatch />} />
      </Routes>
    </Router>
  );
};

export default AppRoutes;
