import { ChangeEvent, useState } from 'react';
import useAuth from '../../hooks/useAuth';
import { Avatar, Button, Switch, Text, View } from '../../lib';
import {
  getProfile,
  pauseRecurringPayment,
  resumeRecurringPayment,
} from '../../service';
import {
  currencyFormat,
  errorHandler,
  getAssetUrl,
  successHandler,
} from '../../utils';
import Dialog from '../common/Dialog';

interface RecurringPaymentProps {
  recurringPayment: RecurringPayment;
  refetchRecurringPayments: VoidFunction;
}

const RecurringPayment = ({
  recurringPayment,
  refetchRecurringPayments,
}: RecurringPaymentProps) => {
  const [isOpen, setIsOpen] = useState(false);
  const [subscribed, setSubscribed] = useState(
    !recurringPayment.pauseCollectionBehavior
  );
  const { user, setUser } = useAuth();

  const handleToggleSubscription = ({
    target: { checked },
  }: ChangeEvent<HTMLInputElement>) => {
    if (!user.stripePaymentMethodId) {
      setIsOpen(true);
      return;
    }
    setSubscribed(checked);
    if (checked) {
      resumeRecurringPayment(recurringPayment.id)
        .then((response) => {
          getProfile().then(setUser).catch(errorHandler);
          // refetchRecurringPayments();
          successHandler(response);
        })
        .catch((error) => {
          setSubscribed(!checked);
          errorHandler(error);
        });
    } else {
      pauseRecurringPayment(recurringPayment.id)
        .then((response) => {
          getProfile().then(setUser).catch(errorHandler);
          // refetchRecurringPayments();
          successHandler(response);
        })
        .catch((error) => {
          setSubscribed(!checked);
          errorHandler(error);
        });
    }
  };

  return (
    <View className='p-4 bg-white border border-gray-300 shadow-md rounded-2xl'>
      <View row className='space-x-3'>
        <Avatar
          src={getAssetUrl(recurringPayment.organization.profileImage)}
          name={recurringPayment.organization.name}
          size='x-small'
        />
        <div>
          <Text type='h3' className='mb-1'>
            {recurringPayment.organization.name}
          </Text>
          <Text type='small'>
            {recurringPayment.organization.address.formatted_address}
          </Text>
        </div>
      </View>
      <View row className='items-center justify-between mt-2 '>
        <p>
          Donation amount:{' '}
          <span className='font-medium text-primary-500'>
            {currencyFormat(recurringPayment.amount / 100)}
          </span>
        </p>
        <Switch checked={subscribed} onChange={handleToggleSubscription} />
      </View>
      <Dialog isOpen={isOpen} persistent transition='zoom-in'>
        {() => (
          <View
            container
            className='p-6 space-y-4 text-center lg:min-w-[448px]'
          >
            <Text type='h1'>Payment Method</Text>
            <Text>
              You don't have any Payment Method added. Please add Payment Method
              to enable recurring payments.
            </Text>
            <Button onClick={() => setIsOpen(false)} className='mx-auto w-max'>
              Got it!
            </Button>
          </View>
        )}
      </Dialog>
    </View>
  );
};

export default RecurringPayment;
