import { Link } from 'react-router-dom';
import { Button, Text, View } from '../../../lib';
import { useQuery } from 'react-query';
import { getStripeAccount } from '../../../service';
import SavedCardBrand, { CardBrand } from './SavedCardBrand';

const SavedBankingCard = () => {
  const { isLoading, data } = useQuery('bank-info', getStripeAccount);

  if (!data?.external_accounts.data[0] && !isLoading) {
    return (
      <View container className='space-y-4'>
        <Text type='h5' className='text-center text-red-500'>
          To start receiving donations or creating campaigns, please add your
          banking information.
        </Text>
        <Button tag={Link} to='/settings'>
          Go to Settings
        </Button>
      </View>
    );
  }

  return (
    <View
      row
      className='items-start justify-between p-4 bg-white border border-gray-300 rounded-lg shadow-lg'
    >
      <div className='px-4 py-0 bg-gray-200 border border-gray-300 rounded-xl'>
        <SavedCardBrand brand={data?.type as CardBrand} />
      </div>
      <View className='text-right'>
        <Text type='h2'>
          **** **** **** {data?.external_accounts.data[0]?.last4}
        </Text>
        <Text type='h2' className='text-gray-400 capitalize'>
          {data?.external_accounts.data[0]?.bank_name}
        </Text>
      </View>
    </View>
  );
};

export default SavedBankingCard;
