import { ChangeEvent, forwardRef, InputHTMLAttributes } from 'react';
import clsx from 'clsx';

interface CommonProps extends InputHTMLAttributes<HTMLInputElement> {
  /**
   * this will focus the input
   */
  autoFocus?: boolean;
  /**
   * Defines the disabled state of the switch
   */
  disabled?: boolean;
  /**
   * Additional styles to customize switch
   */
  className?: string;
}

interface SwitchPropsWithDefaultChecked extends CommonProps {
  /**
   * Defines the defaultChecked of the switch
   */
  defaultChecked: boolean;
  /**
   * Defines if the switch is checked
   */
  checked?: never;
  /**
   * Defines the onChange of the switch
   */
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
}

interface SwitchPropsWithChecked extends CommonProps {
  /**
   * Defines if the switch is checked
   */
  checked: boolean;
  /**
   * Defines the onChange of the switch
   */
  onChange: (e: ChangeEvent<HTMLInputElement>) => void;
  /**
   * Defines the defaultChecked of the switch
   */
  defaultChecked?: never;
}

export type SwitchProps =
  | SwitchPropsWithDefaultChecked
  | SwitchPropsWithChecked;

const Switch = forwardRef<HTMLInputElement, SwitchProps>(
  (
    {
      autoFocus = false,
      disabled = false,
      checked,
      defaultChecked,
      onChange,
      className = '',
      ...rest
    },
    ref
  ) => {
    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
      if (defaultChecked !== undefined && checked === undefined) {
        e.target.setAttribute('aria-checked', e.target.checked.toString());
      }

      onChange && onChange(e);
    };

    return (
      <input
        {...rest}
        ref={ref}
        autoFocus={autoFocus}
        type='checkbox'
        className={clsx('switch', className)}
        role='switch'
        aria-checked={(checked ?? defaultChecked) || false}
        checked={checked}
        defaultChecked={defaultChecked}
        disabled={disabled}
        onChange={handleChange}
      />
    );
  }
);

export default Switch;
